import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login.vue'
import Publish from './views/Publish.vue'
import NewsShow from './views/ShowNews.vue'
import Course from './views/Course.vue'
import StuManage from './views/StuManage.vue'
import ClassChat from './views/ClassChat.vue'
import Mymsg from './views/Mymsg.vue'
import StuProgress from './views/StuProgress.vue'
import CourseAdd from './views/CourseAdd.vue'
import CourseUpload from './views/CourseUpload.vue'
import CourseWare from './views/CourseWare.vue'
import Exam from './views/Exam.vue'
import Communicate from './views/Communicate.vue'
import Comment from './views/Comment.vue'
import Homework from './views/HomeWork.vue'
import SelfInfo from './views/SelfInfo.vue'




Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/News/:name',
      name: 'news',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/News.vue')
    },
    {
      path: '/Publish',
      name: 'Publish',
      component: Publish
    },
    {
      path: '/Show',
      name: 'NewsShow',
      component: NewsShow
    },
    {
      path: '/course',
      name: 'Course',
      component: Course
    },
    {
      path: '/StuManage',
      name: 'StuManage',
      component: StuManage
    },
    {
      path: '/StuProgress',
      name: 'StuProgress',
      component: StuProgress
    },
    {
      path: '/CourseAdd',
      name: 'CourseAdd',
      component: CourseAdd
    },
    {
      path: '/CourseUpload',
      name: 'CourseUpload',
      component: CourseUpload
    },
    {
      path: '/CourseWare',
      name: 'CourseWare',
      component: CourseWare
    },
    {
      path: '/Exam/:name',
      name: 'Exam',
      component: Exam
    },
    {
      path: '/Communicate/:name',
      name: 'Communicate',
      component: Communicate
    },
    {
      path: '/ClassChat',
      name: 'ClassChat',
      component: ClassChat
    },
    {
      path: '/Mymsg',
      name: 'Mymsg',
      component: Mymsg
    },
    {
      path: '/Comment',
      name: 'Comment',
      component: Comment
    },
    {
      path: '/Homework',
      name: 'Homework',
      component: Homework
    },
    {
      path: '/SelfInfo',
      name: 'SelfInfo',
      component: SelfInfo
    }
  ]
})
