<template>
	<div class="course">
		<ul class="newsHead">
			<li>新消息</li>
  		</ul>
  		<div :style="{display: noData? 'none':'block'}">
  			<ul class="showCourse">
	  			<li v-for="(item,index) in DataList" :key="index" @click="gotoPart(index)">
	  				<el-badge :is-dot="item.isRead" class="item dot">
	  					<p>{{item.name}}{{item.tipmsg}}:</p>
						<p>{{item.content}}…</p>
						<p>发布时间：{{item.time}}</p>
	  				</el-badge>
	  			</li>
	  		</ul>
	  		<div class="paging">
		  		<el-pagination
		  		  v-if='ispage'
			      @current-change="handleCurrentChange"
			      :current-page.sync="activePage"
			      :page-size="6"
			      :pager-count="5"
			      layout="prev, pager, next"
			      :total="totalPage">
			    </el-pagination>
		  	</div>
  		</div>
  		<div :style="{display: noData? 'block':'none'}">
	  		<div class="notice">
		  		<img src="../assets/img/nodata.png">
		  		<p>暂无内容！</p>
		  	</div>
	  	</div>
	</div>
</template>

<script>
export default {
	name: 'course',
	data(){
		return {
			param: new FormData(),
			dialogVisible: false,
			ispage: false,
			noData: true,
			activePage: 1,
			totalPage: 1,
			classUrl: '',
			pubForm:{
				id: '',
				title: '',
				content: ''
			},
			DataList:[]
		}
	},
    mounted(){
    	if (this.getToken()) {
			this.getListParam();
		}
    },
	methods:{
		//为获取班级列表添加参数
		getListParam(){
			var nowPage = sessionStorage.getItem('nowPage');
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.set('token',this.getToken());
			this.param.set('currentPage',this.activePage);
			this.param.set('pageNum',6);
			this.classUrl = this.$baseUrl + '/messages';
			this.realGotData();
		},
		//获取班级列表
		realGotData(){
			var that = this;
			this.axios.post(this.classUrl,this.param)
			.then((response)=>{
	      		var res = response.data;
				// console.log(res);
		        var theJSON = {};
		        that.DataList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		name: perData.name,
		        		content: perData.content,
		        		contentId: perData.contentId,
		        		category: perData.category,
		        		tipmsg: '',
		        		time: perData.time,
		        		isRead: perData.isRead == 0? true: false
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	console.log(res.data)
		        	that.totalPage = res.data.totalNumber;
		        	if (Array.isArray(res.data.pageData)) {
		        		for(var key of res.data.pageData){
		        			let newone = formatData(key);
		        			if (key.category == 'course') {
		        				newone.tipmsg = '（课程区）回复了您';
		        			} else if (key.category == 'class') {
		        				newone.tipmsg = '（班级区）回复了您';
		        			} else if (key.category == 'notice') {
		        				newone.tipmsg = '发布了通知';
		        			} else {
		        				newone.tipmsg = '发布了作业';
		        			}
		        			that.DataList.push(newone);
			        	}
			        	that.noData = false;
		        	} else {
		        		that.$message.warning('新列表为空！');
		        	}
		        	//大于一页才显示分页
		        	if (that.totalPage > 6) {
		        		that.ispage = true;
		        	}
		        } else {
		        	that.$message.error('获取'+ that.name +'列表失败！');
		        }
		     })
			.catch((e)=>{
				console.log(e);
			})
		},
		//未读变已读
		MarkRead(type,id){
			var url = this.$baseUrl + '/markAsRead';
			var param = new FormData();
			param.set('token',this.getToken());
			param.set('msgUserId',id);
			if (type == 'course' || type == 'class') {
				param.set('type',1);
			} else {
				param.set('type',0);
			}
			this.axios.post(url,param)
			.then((res)=>{
				console.log(res);
			})
			.catch((e)=>{
				console.log(e);
			})
		},
		//从班级进入发言列表
		gotoPart(index){
			console.log(this.DataList[index].contentId)
			var IDs = this.DataList[index].contentId.split("_");
	      	// console.log(IDs);
	     	var contentId = IDs[0];
	        var courseId = IDs[1];
			var type = this.DataList[index].category;
			if (this.DataList[index].isRead) {
				this.MarkRead(type,this.DataList[index].id);
			}
			switch(type) {
				case 'course':
					sessionStorage.setItem('courseID', JSON.stringify(parseInt(courseId)));
					this.$router.push({
						path: `/Comment`,
						query:{
							id: contentId,
							name: 'msg'
						}
					})
					break;
				case 'class':
					this.$router.push({
						path: `/Comment`,
						query:{
							id: contentId,
							name: '123'
						}
					})
					break;
				case 'homework':
					break;
				case 'notice':
					break;
				default :
					break;
			}
		},
		//获取token验证,token 不能有" "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	},
    	//分页跳转
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			//更新路由参数的值
			this.param.set('currentPage',this.activePage);
			this.realGotData();
			//保存页码
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
		},
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
p{
	margin: 0;
	padding: 0;
}
.course{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 70*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			height: 45*@allwidth;
			line-height: 45*@allwidth;
			width: 100*@allwidth;
			color: #fff;
			background: #099c4a;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 20*@allwidth;
		}
	}
	.showCourse{
		width: 100%;
		margin-top: 5*@allwidth;
		display: flex;
		flex-wrap: wrap;
		li {
			width: 95%;
			.dot{
				width: 100%;
				height: 50*@allwidth;
				border: 1.5px solid #D2D2D2;
				border-radius: 5px;
				margin: 5*@allwidth 0.6%;
				padding: 0 20*@allwidth;
				cursor: pointer;
				font-size: 18*@allwidth;
				display: flex;
				align-items: center;
				p{
					width: 25%;
				}
				p:nth-child(2){
					width: 50%;
				}
			}
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}
.notice{
	width: 99%;
	margin: 10*@allwidth auto;
	height: calc(100vh - 190*@allwidth);
	min-height: 200px;
	box-shadow: 0 0 0.3px 0.3px #5D8486;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	img{
		width: 70px;
		height: 70px;
	}
	p{
		font-size: 18*@allwidth;
		margin: 5*@allwidth;
		color: #5D8486;
	}
}
</style>