<template>
	<div class="UpCourseWare">
		<ul class="newsHead">
			<li>课程列表</li>
			<li @click="goWare()">上传课件</li>
			<li @click="addCHP">创建章节</li>
		</ul>
		<el-row  :style="{display: noData? 'none':'block'}">
		  <el-col :span="6">
		  	<div class="courseTab">
		  		<ul v-for="(sec,index) in section" :key="index">
		  			<li @click="ClickSec(index)">
		  				<p :title="sec.name">第{{index + 1}}章：{{sec.name}}</p>
		  				<i :class="sec.showMore?'el-icon-arrow-up':'el-icon-arrow-down'"></i>
		  			</li>
		  			<li v-show="sec.showMore"
		  			 @click="ChoiceChp(index,indx)" 
		  			 :class="[(choseSecChp.secIndex == index && choseSecChp.chpIndex == indx) ? 'isActive':'']" 
		  			 v-for="(chp,indx) in sec.chapter" 
		  			 :key="indx">
		  				<p :title="chp.title">第{{indx + 1}}节：{{chp.title}}</p>
		  			</li>
		  			<li v-show="sec.showMore" @click="appchp(index)">
		  				<i class="el-icon-circle-plus"></i>
		  				新增小节
		  			</li>
		  		</ul>
		  	</div>
		  </el-col>
		  <el-col :span="18" v-if="ifuploaded">
		  	<div class="upVideo">
		  		<div class="SEC">
		  			<p>{{secname}}</p>
		  			<i class="el-icon-edit-outline" @click="resetName()" >
		  				<!-- title="更改新章名称，设置权重" -->
		  			</i>
		  			<div class="operate">
		  				<i class="el-icon-delete" @click="Delete()"></i>
		  			</div>
		  		</div>
		  		<ul class="EditName" v-show="choseSecChp.theGrade == 'SEC'? 
			  		section[choseSecChp.secIndex].edit : 
			  		section[choseSecChp.secIndex].chapter[choseSecChp.chpIndex].edit">
		  			<li>
		  				<p>设置章节名</p>
		  				<input v-model="resetWeight.name" type="text" placeholder="请输入章节名" :maxlength="15">
		  			</li>
		  			<li>
		  				<p>权重</p>
		  				<input v-model="resetWeight.weight" type="text" :maxlength="2" placeholder="请输入1-99权值" oninput="value=value.replace(/[^\d]/g,'')">
		  			</li>
		  			<li class="confirm">
		  				<p @click="noResetName()" style="background: grey;color: #fff;">取消</p>
		  				<p @click="setPerName()">确定</p>
		  			</li>
		  		</ul>
		  		<div v-if="choseSecChp.theGrade == 'chp'" class="showVideo">
		  			<div v-if="section[choseSecChp.secIndex].chapter[choseSecChp.chpIndex].url && noEdit" class="haveVideo">
		  				<video :src="imgUrl+ section[choseSecChp.secIndex].chapter[choseSecChp.chpIndex].url" alt="路径错误！"  controls="controls"></video>
		  			</div>
		  			<div v-else class="uploadVideo">
		  				<el-upload class="avatar-uploader el-upload--text" 
			  			:action="uploadUrl" 
			  			:show-file-list="false" 
			  			:data="videoData"
			  			accept=".mp4"
			  			:on-success="handleVideoSuccess" 
			  			:before-upload="beforeUploadVideo" 
			  			:on-progress="uploadVideoProcess">
					        <div v-if="videoFlag == false" style="width: 80px; height: 80px;border: 1px solid grey;border-radius: 5px;display: flex;align-items: center;justify-content: center;">
					        	<i class="el-icon-plus avatar-uploader-icon" style="font-size: 30px;color: grey;"></i>
					        </div>
					    </el-upload>
					    <el-progress v-if="videoFlag == true" type="circle" style="margin-top:30px;" :percentage="videoUploadPercent"></el-progress>
					    <p class="text">请上传MP4格式视频！</p>
		  			</div>
		  		</div>
		  	</div>
		  </el-col>
		</el-row>
		<div :style="{display: noData? 'block':'none'}">
	  		<div class="notice">
		  		<img src="../assets/img/nodata.png">
		  		<p>暂无内容！</p>
		  	</div>
	  	</div>
	</div>
</template>

<script>
export default {
	name: 'UpCourseWare',
	data(){
		return{
			imgUrl: '',
			getListUrl: '',
			param: new FormData(),
			noData: true,
			uploadUrl: '',
			noEdit: true,
			videoFlag: false,
			videoUploadPercent: 0,
			videoData:{},
			course:{
				name: '',
				id: '',
			},
			choseSecChp: {
				theGrade: 'SEC',
				secIndex: '0',
				chpIndex: '0'
			},
			resetWeight: {
				name: '',
				weight: ''
			},
			secname: '',
			ifuploaded: false,
			isChp: false,
			originSec:{
				showMore: true,
				name: '新一章',
				edit: false,
				chapter:[{
					title: '新一节',
					videoUrl: '',
					edit: false
				}]
			},
			orginchp:{
				title: '新一节',
				videoUrl: '',
				edit: false
			},
			section:[]
		}
	},
	created(){
		if (this.getToken()) {
			this.addGetParam();
			this.imgUrl = this.$ImgUrl + '/video/';
		}
	},
	methods: {
		//为获取章节名添加参数
		addGetParam(){
			var url = this.$baseUrl + '/courseInfo/';
			this.course.id = sessionStorage.getItem('courseID');
			this.course.name = sessionStorage.getItem('courseName');
			this.param.set('token',this.getToken());
			this.getListUrl = url+this.course.id;
			this.getCourseTitle();
		},
		//获取章节名
		getCourseTitle(secIndexs){
			var that = this;
			this.axios.post(this.getListUrl,this.param)
	      	.then((response)=>{
	      		var res = response.data;
	      		console.log(res.data);
		        var theJSON = {};
		        that.section = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		title: perData.title,
		        		url: perData.url,
		        		edit: false,
		        		weights: perData.weights
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	//循环section章
		        	if (res.data.chapterDtoList.length > 0) {
		        		for(var key of res.data.chapterDtoList){
		        			var sCHP = {
		        				showMore: false,
		        				edit: false,
		        				id: key.id,
		        				name: key.title,
		        				weights: key.weights,
		        				chapter: []
		        			};
		        			//循环chapter节
		        			if (Array.isArray(key.sectionList)) {
		        				for( var innet of key.sectionList){
		        					sCHP.chapter.push(formatData(innet));
		        				}
		        				that.ifuploaded = true;
		        			} else {
		        				console.log('no chapter!');
		        			}
			        		that.section.push(sCHP);
			        	}
		        		that.noData = false;
		        	} else {
		        		that.ifuploaded = false;
		        		that.$message.warning('暂无章节');
		        		return ;
		        	}
		        } else {
		        	that.$message.error('获取'+ that.name +'列表失败！');
		        }
		        if (secIndexs) {
		        	that.ClickSec(secIndexs);
		        } else {
		        	//默认展开第一章
		        	that.ClickSec(0);
		        }
		        
		     })
			.catch((e)=>{
	      		// console.log('请求错误！')
				that.$message.warning(e);
			})
		},
		//获取token验证,token 不能有" "
		getToken(){
			var theToken = sessionStorage.getItem('token');
			return theToken.slice(1,theToken.length-1);
		},
		//上传视频前检验
		beforeUploadVideo(file) {
			let chps = this.section[this.choseSecChp.secIndex].chapter[this.choseSecChp.chpIndex];
			if (chps.id) {
				// console.log(chps.id);
				if (['video/mp4'].indexOf(file.type) == -1) {
			        this.$message.error('请上传正确的视频格式');
			        return false;
			    }
			} else {
				//创建视频
				var ifcreact = this.videoData.title.length > 0 && this.videoData.weights.length > 0;
				if (!ifcreact) {
					this.$message.error('创建小节请先设置小节名和权重！');
					return false;
				} 
			}
		    
		},
		//上传过程
		uploadVideoProcess(event, file, fileList){
		    this.videoFlag = true;
		    this.videoUploadPercent =  parseInt(file.percentage.toFixed(0));
		},
		//视频上传成功
		handleVideoSuccess(res, file) {
			//获取上传图片地址
		    console.log(res);
		    this.videoFlag = false;
		    this.videoUploadPercent = 0;
		    if(res.code == 200){
		    	this.$message.success('视频上传成功！');
				this.noEdit = true;
				this.getCourseTitle();
		    }else{
		        this.$message.error('视频上传失败，请重新上传！');
		    }
		},
		//手动添加章
		addCHP(){
			var that = this;
			var addSec = new FormData();
			this.ifuploaded = true;
			if (this.section.length > 0) {
				var index = this.section.length-1;
				var lastSec = this.section[index].weights;
				console.log(lastSec,index)
				addSec.set('weights',(lastSec + 1));
			} else {
				addSec.set('weights',1);
			}
			this.section.push(this.originSec);
			addSec.set('title',this.originSec.name);
			addSec.set('token',this.getToken());
			addSec.set('superiorId',this.course.id);
			this.axios.post(this.$baseUrl+'/chapter/create',addSec)
			.then((res)=>{
				console.log(res.data);
				that.getCourseTitle();
			})
		},
		//添加节
		appchp(index){
			this.section[index].chapter.push(this.orginchp)
		},
		//章节列表的显示隐藏,并控制选择某章
		ClickSec(index){
			this.section[index].showMore = !this.section[index].showMore;
			this.isChp = false;
			this.choseSecChp.theGrade = 'SEC';
			this.choseSecChp.secIndex = index;
			//选择某章
			this.secname = '第' + (index + 1) + '章  ' + this.section[index].name;
		},
		//设置章节名,并保存到后台
		resetName(){
			if (this.choseSecChp.theGrade == 'SEC') {
				this.section[this.choseSecChp.secIndex].edit = true;
				var secs = this.section[this.choseSecChp.secIndex];
				this.resetWeight.name = secs.name;
				this.resetWeight.weight = secs.weights;
			} else {
				this.noEdit = false;
				this.section[this.choseSecChp.secIndex].chapter[this.choseSecChp.chpIndex].edit = true;
				var chps = this.section[this.choseSecChp.secIndex].chapter[this.choseSecChp.chpIndex];
				if (chps.id) {
					this.uploadUrl = this.$baseUrl+'/section/update';
					this.resetWeight.name = chps.title;
					this.resetWeight.weight = chps.weights;
					var chpData = {
						id: chps.id,
						token: this.getToken(),
						superiorId: this.section[this.choseSecChp.secIndex].id
					}
					this.videoData = chpData;
				}
				
			} 
		},
		//取消
		noResetName(){
			if (this.choseSecChp.theGrade == 'SEC') {
				this.section[this.choseSecChp.secIndex].edit = false;
			} else {
				this.noEdit = true;
				this.section[this.choseSecChp.secIndex].chapter[this.choseSecChp.chpIndex].edit = false;
			} 
		},
		//确定更改章节名，并保存到数据库
		setPerName(){
			var that = this;
			if (this.resetWeight.name == '') {
				this.$message.warning('标题不能为空！');
				return ;
			}
			if (this.resetWeight.weight == '') {
				if (this.resetWeight.weight != 0) {
					this.$message.warning('权重不能为空！');
				} else {
					this.$message.warning('请设置权重1-100！');
				}
				
				return;
			}
			//修改章
			if (this.choseSecChp.theGrade == 'SEC') {
				var addSec = new FormData();
				let secIndex = this.choseSecChp.secIndex;
				let secs = this.section[secIndex];
				if (this.resetWeight.name == secs.name && this.resetWeight.weight == secs.weights) {
					console.log('the same');
					this.$message.warning('请确认有修改内容！');
					return ;
				}
				if (this.resetWeight.name != secs.name) {
					addSec.set('title',this.resetWeight.name);
				}
				if (this.resetWeight.weight != secs.weights) {
					addSec.set('weights',this.resetWeight.weight);
				}
				addSec.set('token',this.getToken());
				addSec.set('id',this.section[secIndex].id);
				this.axios.post(this.$baseUrl+'/chapter/update',addSec)
				.then((res)=>{
					console.log(res.data);
					that.getCourseTitle();
				})
				this.section[this.choseSecChp.secIndex].edit = false;
			} 
			//添加修改小节
			else {
				let chps = this.section[this.choseSecChp.secIndex].chapter[this.choseSecChp.chpIndex];
				if (chps.id) {
					//修改小节
					//动态添加对象的属性
					// this.$set(this.videoData, 'id', chps.id);
					// var data = {

					// }
					var addchp = new FormData();
					if (this.resetWeight.name == chps.title && this.resetWeight.weight == chps.weights) {
						this.$message.warning('请确认有修改内容！');
						return ;
					}
					if (this.resetWeight.name != chps.title) {
						addchp.set('title',this.resetWeight.name);
					}
					if (this.resetWeight.weight != chps.weights) {
						addchp.set('weights',this.resetWeight.weight);
					}
					addchp.set('token',this.getToken());
					addchp.set('superiorId',this.section[this.choseSecChp.secIndex].id);
					addchp.set('id',chps.id);
					this.uploadUrl = this.$baseUrl+'/section/update';
					this.axios.post(this.uploadUrl,addchp)
					.then((res)=>{
						console.log(res.data);
						var sec = that.choseSecChp;
						that.getCourseTitle(sec.secIndex);
					})
				} else {
					// 添加小节
					this.uploadUrl = this.$baseUrl+'/section/create';
					var data = {
						token: this.getToken(),
						title: this.resetWeight.name,
						weights: this.resetWeight.weight,
						superiorId: this.section[this.choseSecChp.secIndex].id
					}
					this.videoData = data;
				}
				this.section[this.choseSecChp.secIndex].chapter[this.choseSecChp.chpIndex].edit = false;
			}
		},
		//选择了某章某节，保留章节的index
		ChoiceChp(sec,chp){
			if (this.choseSecChp.theGrade == 'SEC') {
				this.section[this.choseSecChp.secIndex].edit = false;
			} else {
				this.section[this.choseSecChp.secIndex].chapter[this.choseSecChp.chpIndex].edit = false;
			}
			this.isChp = true;
			this.noEdit = true;
			this.choseSecChp.theGrade = 'chp';
			this.choseSecChp.secIndex = sec;
			this.choseSecChp.chpIndex = chp;
			this.secname = (sec+1) + '-' + (chp+1) + '节：' + this.section[sec].chapter[chp].title;
		},
		//删除章节
		Delete(){
			var that = this;
			this.$confirm('是否确认删除该章节？')
			.then(()=>{
				var index = that.choseSecChp;
				if (index.theGrade == 'SEC') {
					that.DeleteDOM(that.$baseUrl+'/chapter/delete/'+ that.section[index.secIndex].id);
				} else {
					if (that.section[index.secIndex].chapter[index.chpIndex].id) {
						that.DeleteDOM(that.$baseUrl+'/section/delete/'+ that.section[index.secIndex].chapter[index.chpIndex].id);
					} else {
						that.getCourseTitle();
					}
					
				}
			})
		},
		//删除章节的具体函数
		DeleteDOM(url){
			var that = this;
			var dparam = new FormData();
			dparam.set('token',this.getToken());
			this.axios.post(url,dparam)
			.then((res)=>{
				console.log(res.data);
				if (res.data.code == 200) {
					that.$message.success('删除成功！');
					that.getCourseTitle();
				} else {
					that.$message.error('删除失败！');
				}
			})
		},
		//大标题选中时背景颜色
		goWare(){
			this.$router.push('/CourseWare');
		}
    }
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
p{
	margin: 0;
	padding: 0;
}
.UpCourseWare{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 70*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			height: 45*@allwidth;
			line-height: 45*@allwidth;
			padding: 0 15*@allwidth;
			color: #fff;
			background: #099c4a;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 20*@allwidth;
		}
		li:nth-child(2){
			background: #90a7a6;
		}
		li:nth-child(3){
			margin-left: auto;
			background: #28acc1;
		}
	}
	.courseTab{
		margin-top: 5*@allwidth;
		ul{
			width: 100%;
			margin: 1px 0;
			border-radius: 5px;
			border: 1px solid #D2D2D2;
			li{
				height: 40*@allwidth;
				line-height: 40*@allwidth;
				font-size: 16*@allwidth;
				padding: 0 2*@allwidth;
				cursor: pointer;
				border-top: 0.3px solid #D2D2D2;
				p{
					margin-left: 15%;
					//超出文字省略
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
					//end
				}
			}
			li:first-child{
				background: #dcdfe6;
				height: 45*@allwidth;
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 0 15*@allwidth;
				font-size: 18*@allwidth;
				p{
					margin: 0;
				}
			}
			li:last-child{
				text-align: center;
			}
			.isActive{
				color: #409EFF;
				background: #F3F6F6;
			}
		}
	}
	.upVideo{
		width: 97%;
		margin: 0 auto;
		margin-top: 5*@allwidth;
		border: 1px solid grey;
		border-radius: 5px;
		.SEC{
			width: 95%;
			margin: 15*@allwidth auto;
			display: flex;
			font-size: 20*@allwidth;
			.operate{
				margin-left: auto;
			}
			i{
				margin: 0 5*@allwidth;
				cursor: pointer;
			}
			i:hover{
				color: #98E26E;
			}
		}
		.EditName{
			width: 95%;
			margin: 0 auto;
			padding: 10*@allwidth 0;
			border-top: 1px solid green;
			display: flex;
			justify-content: space-between;
			align-items: center;
			li{
				display: flex;
				align-items: center;
				font-size: 18*@allwidth;
				p{
					margin: 0 15*@allwidth;
				}
				input{
					outline: none;
					border: 0.5px solid black;
					border-radius: 3px;
					height: 30*@allwidth;
					padding-left: 5*@allwidth;
				}
				input:hover{
					border-color: #409EFF;
				}
			}
			.confirm{
				p{
					margin: 0 5*@allwidth;
					line-height: 34*@allwidth;
					background: #409EFF;
					color: #fff;
					border-radius: 5px;
					padding: 0 10*@allwidth;
					cursor: pointer;
				}
			}
		}
		.showVideo{
			width: 95%;
			margin: 0 auto;
			padding-top: 10*@allwidth;
			border-top: 1px solid green;
			.haveVideo{
				width: 100%;
				height: calc(100% - 30*@allwidth);
				display: flex;
				align-items: center;
				justify-content: center;
				video{
					width: 90%;
				}
			}
			.uploadVideo{
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				margin: 30*@allwidth 0;
				p{
					margin: 10*@allwidth 0;
				}
			}
		}
	}
}
.notice{
	width: 99%;
	margin: 10*@allwidth auto;
	height: calc(100vh - 190*@allwidth);
	min-height: 200px;
	box-shadow: 0 0 0.3px 0.3px #5D8486;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	img{
		width: 70px;
		height: 70px;
	}
	p{
		font-size: 18*@allwidth;
		margin: 5*@allwidth;
		color: #5D8486;
	}
}
</style>