<template>
	<div class="selfInfo">
		<div class="pubHead">
			<p>个人信息</p>
			<ul>
				<li v-if="!isEdit" @click="upInfo()">编辑</li>
				<li v-if="isEdit" @click="cancle()">取消</li>
				<li v-if="isEdit" @click="UpDataInfo()">确定</li>
			</ul>
		</div>
		<div v-if="!isEdit" class="showInfo">
			<div class="ReUpImg">
				<img :src="imgUrl + $store.state.cover + '?number=' + $store.state.random" alt="图片错误">
				<div>
					<el-upload
					  class="avatar-uploader"
					  :action="$baseUrl+'/uAvatar'"
					  auto-upload
					  accept="*.png,*.jpg,*.jpeg"
					  :show-file-list="false"
					  :on-success="handleSuc"
					  :on-error="handleErr"
					  :data="ImgCover">
					    <i slot="default" class="el-icon-edit" title="修改图片"></i>
					</el-upload>
				</div>
			</div>
			
			<ul>
				<li>{{this.$store.state.name}}</li>
				<li>
					<p>账号：{{teacher.username}}</p>
					<p>邮箱：{{teacher.email}}</p>
				</li>
			</ul>
		</div>
		<div v-if="isEdit" class="upInfo">
			<el-form :model="ruleForm" ref="ruleForm" :rules="rules" label-width="100px" class="demo-ruleForm" label-position="left">
				<el-form-item label="修改昵称" prop="name">
				    <el-input v-model="ruleForm.name" :maxlength="4" placeholder="请输入新昵称"></el-input>
				</el-form-item>
				<el-form-item label="修改密码" prop="psd">
				    <el-input v-model="ruleForm.psd" placeholder="请输入新密码"></el-input>
				</el-form-item>
				<el-form-item label="修改邮箱" prop="email">
				    <el-input v-model="ruleForm.email" placeholder="请输入新邮箱"></el-input>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>

<script>
export default {
	name: 'selfInfo',
	data(){
		var validateName = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error('昵称不能为空'));
	        } else {
	          callback();
	        }
	    };
	    var validatePwd = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error('新密码不能为空'));
	        } else {
	          callback();
	        }
	    };
	    var validateEmail = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error('邮箱不能为空'));
	        } else {
	        	var pattern = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
	        	// console.log('the email is ' + pattern.test(value));
	        	// console.log(pattern.test(value) == true)
	        	if(pattern.test(value) == true) {
	        		this.isformatEmail = true;
	        		callback();
		        } else {
		        	callback(new Error('请输入正确格式邮箱！'));
		        	this.isformatEmail = false;
		        }
	        }
	    };
		return {
			imgUrl: '',
      		param: new FormData(),
      		isEdit: false,
      		ImgCover:{
      			token: ''
      		},
			ruleForm:{
				name: '',
				email: '',
				cover:'',
				id: '',
				psd: ''
			},
			teacher:{
				name: '',
				email: '',
				cover:'',
				id: '',
				username: ''
			},
			isformatEmail: '',
			rules: {
	          name: [
	            { validator: validateName, trigger: 'blur' }
	          ],
	          psd: [
	            { validator: validatePwd, trigger: 'blur' }
	          ],
	          email: [
	            { validator: validateEmail, trigger: 'blur' }
	          ]
	        },
		}
	},
	mounted(){
		if (this.getToken()) {
			this.getInfoParam();
			this.imgUrl = this.$ImgUrl + '/avatar/';
			this.ImgCover.token = this.getToken();
		}
	},
	methods:{
		//获取老师信息
		getInfoParam(){
			this.param.set('token',this.getToken());
			this.getInfo();
		},
		getInfo(){
			var that = this;
			var url = this.$baseUrl + '/info';
			this.axios.post(url,this.param)
			.then((res)=>{
				res = res.data;
				console.log(res);
				if (res.code == 200) {
					that.teacher.id = res.data.id;
					that.teacher.email = res.data.email;
					that.teacher.name = res.data.name;
					that.teacher.username = res.data.username;
					if (that.$store.state.name != res.data.name) {
						that.$store.commit('name', res.data.name);
					}
				}
			})
			.catch((e)=>{
				console.log(e);
			})
		},
	    //取消
	    cancle(){
	    	this.isEdit = false;
	    },
	    //更改身份信息
	    upInfo(){
	    	this.isEdit = true;
	    	this.ruleForm.name = this.teacher.name;
	    	this.ruleForm.email = this.teacher.email;
	    },
	    //更改具体身份信息
	    UpDataInfo(){
	    	var that = this;
	    	var param = new FormData();
	    	if (this.ruleForm.name == this.teacher.name && this.ruleForm.email == this.teacher.email && this.ruleForm.psd.length == 0) {
	    		this.$message.error('请确定有修改内容！');
	    		return ;
	    	}
	    	if (this.ruleForm.name != this.teacher.name) {
				param.set('name',this.ruleForm.name);
	    	}
	    	if (this.ruleForm.email != this.teacher.email) {
	    		if (this.isformatEmail == false) {
	    			this.$message.error('请输入正确格式邮箱！');
	    			return ;
	    		}
	    		param.set('email',this.ruleForm.email);
	    	}
	    	if (this.ruleForm.psd) {
				if (this.ruleForm.psd.length < 6) {
			    	this.$message.error('密码不能少于6位！');
		    	} else {
		    		param.set('password',this.ruleForm.psd);
		    	}
	    	}
	    	param.set('token',this.getToken());
			var url = this.$baseUrl + '/uInfo/teacher';
			this.axios.post(url,param)
			.then((res)=>{
				res = res.data;
				console.log(res);
				if (res.code == 200) {
					that.$message.success('更改成功！');
					that.isEdit = false;
					that.ruleForm.name ='';
					that.ruleForm.psd ='';
					that.ruleForm.email ='';
					that.getInfo();
				}
			})
			.catch((e)=>{
				console.log(e);
			})
	    },
	    //更改头像
		handleSuc(res, file, fileList){
			console.log(res);
			if (res.code == 200) {
				this.$store.commit('random',Math.random());
				this.$message.success('更换成功！');
			}
		},
		handleErr(err, file, fileList){
			console.log(err);
			if (fileList.length == 0) {
				console.log('上传失败！')
			}
			console.log(fileList);
		},
	    //获取token验证,token 不能有" "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
p{
	margin: 0;
	padding: 0;
}
.selfInfo{
	width: 95%;
	margin: 0 auto;
	.pubHead{
		height: 50*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		p{
			font-size: 25*@allwidth;
		}
		ul{
			margin-left: auto;
			display: flex;
			li{
				text-align: center;
				height: 35*@allwidth;
				line-height: 35*@allwidth;
				padding: 2*@allwidth 20*@allwidth;
				color: #fff;
				background: #3D7E8D;
				cursor: pointer;
				margin: 0 3*@allwidth;
				border-radius: 5px;
				font-size: 18*@allwidth;
			}
		}
		
	}
	.showInfo{
		margin-top: 30*@allwidth;
		border: 1px solid green;
		border-left: 10*@allwidth solid green;
		border-radius: 5*@allwidth;
		padding: 20*@allwidth 20*@allwidth;
		display: flex;
		align-items: center;
		img{
			width: 100*@allwidth;
			height: 100*@allwidth;
			border-radius: 50%;
		}
		ul{
			margin-left: 30*@allwidth;
			li{
				font-size: 18*@allwidth;
				margin: 20*@allwidth 0;
				display: flex;
				p{
					font-size: 16*@allwidth;
					margin-right: 30*@allwidth;
				}
			}
		}
	}
	.upInfo{
		width: 60%;
		margin: 60*@allwidth;
		img{
			background-color: #fbfdff;
		    border: 0.026667rem dashed #c0ccda;
		    border-radius: 0.16rem;
		    box-sizing: border-box;
		    width: 3.946667rem;
		    height: 3.946667rem;
		    line-height: 3.893333rem;
		    vertical-align: top;
		}
	}
}
.ReUpImg{
	display: flex;
	align-items: flex-end;
}
</style>