<template>
	<div class="course">
		<ul class="newsHead">
			<li v-if="name != 'msg'"><i class="el-icon-back" @click="goBack()"></i></li>
			<li v-if="name != 'msg'">{{title}}</li>
			<li v-if="name == 'msg'" style="background: #099c4a;color: #fff;border-radius: 5px;">{{title}}</li>
  			<li @click="dialogVisible = true" >提问</li>
  		</ul>
  		<div :style="{display: noData? 'none':'block'}">
  			<ul class="TieList">
				<li v-for="(item,index) in publishData" :key="index" @click="PartDetail(index)">
					<img :src="imgUrl +item.cover + '?number=' + $store.state.random">
					<div class="part">
						<p>{{item.publisher}}<span v-if="item.rank" class="tags">老师</span><span>{{item.time}}</span></p>
						<p>{{item.title}}</p>
						<p>评论:{{item.commentNum}}</p>
					</div>
				</li>	
			</ul>
	  		<div class="paging">
		  		<el-pagination
		  		  v-if="ispage"
			      @current-change="handleCurrentChange"
			      :current-page.sync="activePage"
			      :page-size="12"
			      :pager-count="5"
			      layout="prev, pager, next"
			      :total="totalPage">
			    </el-pagination>
		  	</div>
  		</div>
  		<div :style="{display: noData? 'block':'none'}">
	  		<div class="notice">
		  		<img src="../assets/img/nodata.png">
		  		<p>暂无内容！</p>
		  	</div>
	  	</div>
  		<el-dialog
  			title="提问"
  			center
		  	:visible.sync="dialogVisible"
		  	width="80%">
		  	<el-form :model="pubForm" class="demo-ruleForm" style="width: 85%;margin: 0 auto;">
				<el-form-item label="话题" prop="name" style="margin-bottom: 0;">
				    <el-input v-model="pubForm.title" placeholder="输入15字内话题" :maxlength="15"></el-input>
				</el-form-item>
				<el-form-item label="内容" prop="desc">
				    <br>
				    <Edit @newsEvent="showInfo"></Edit>
				</el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="canselSpeach()">取消</el-button>
		    <el-button type="primary" @click="isSpeach()">确定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
import Edit from '../components/ue2.vue';
export default {
	name: 'course',
	components:{
		Edit
	},
	data(){
		return {
			imgUrl: '',
			param: new FormData(),
			dialogVisible: false,
			ispage: false,
			noData: true,
			title: '',
			name: '',
			activePage: 1,
			totalPage: 1,
			TieUrl: '',
			pubForm:{
				title: '',
				content: ''
			},
			classList:[{
				id: '2',
				name: '049117'
			}],
			publishData:[]
		}
	},
    mounted(){
    	if (this.getToken()) {
			this.getListParam();
    		this.imgUrl = this.$ImgUrl + '/avatar/';
		}
    },
	methods:{
		//为获取帖子列表添加参数
		getListParam(){
			var name = this.$route.params.name;
			this.name = name;
			if (name == 'msg') {
				this.title = '课程留言';
				var courseId = sessionStorage.getItem('courseID');
				this.param.set('courseId',courseId);
				this.TieUrl = this.$baseUrl + '/courseDiscussion/questions/' + courseId;
			} else {
				this.title = '班级讨论';
				this.TieUrl = this.$baseUrl + '/ClassDiscussion/questions/' + name;
			}
			var nowPage = sessionStorage.getItem('nowPage');
			console.log(nowPage)
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.set('token',this.getToken());
			this.param.set('currentPage',this.activePage);
			this.param.set('pageNum',12);
			this.realGotData();
		},
		//获取帖子列表
		realGotData(){
			var that = this;
			this.axios.post(this.TieUrl,this.param)
			.then((response)=>{
	      		var res = response.data;
		        var theJSON = {};
		        that.publishData = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		TieID: perData.id,
		        		publisher: perData.userDto.name,
		        		cover: perData.userDto.avatar,
		        		title: perData.title,
		        		rank: perData.isTeacher==1?true:false,
		        		commentNum: perData.commentNum,
		        		time: perData.time
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	console.log(res.data)
		        	that.totalPage = res.data.totalNumber;
		        	if (Array.isArray(res.data.pageData)) {
		        		for(var key of res.data.pageData){
			        		that.publishData.push(formatData(key));
			        	}
		        		that.noData = false;
		        	} else {
		        		that.$message.warning(that.name + '列表为空！');
		        	}
		        	if (res.data.totalNumber > 12) {
		        		that.ispage = true;
		        	}  else {
		        		that.ispage = false;
		        	}
		        } else {
		        	that.$message.error('获取'+ that.name +'列表失败！');
		        }
		     })
			.catch((e)=>{
				console.log(e);
			})
		},
		//取消发言
		canselSpeach(){
			this.pubForm.title = '';
			this.pubForm.content = '';
			this.dialogVisible = false;
		},
		isSpeach(){
			if (this.pubForm.title == '' || this.pubForm.content == '') {
				this.$message.error('题目和内容不能为空!');
				return ;
			}
			this.pubSpeach();
		},
		//发言
		pubSpeach(){
			var that = this;
			var pubSpeaParam = new FormData();
			var url = '';
			if (this.name == 'msg') {
				url = this.$baseUrl + '/courseDiscussion/pub';
				pubSpeaParam.set('courseId',this.param.get('courseId'));
			} else {
				url = this.$baseUrl + '/ClassDiscussion/pub';
				pubSpeaParam.set('classId',this.name);
			}
			pubSpeaParam.set('token',this.param.get('token'));
			pubSpeaParam.set('title',this.pubForm.title);
			pubSpeaParam.set('content',this.pubForm.content);
			this.axios.post(url,pubSpeaParam)
			.then((res)=>{
				console.log(res.data);
				if (res.data.code == 200) {
					that.$message.success('发布成功！');
					that.pubForm.title = '';
					that.pubForm.content = '';
					that.dialogVisible = false;
					that.realGotData();
				} else {
					that.$message.error('发布失败！');
				}
			})
			.catch((e)=>{
				console.log(e);
			})
		},
		//从班级进入发言列表
		goCommunity(index){
			this.$router.push({
				path: `/CommunityPart`,
				query:{
					name: this.classList[index].name,
					id: this.classList[index].id
				}
			})
		},
		//从留白区（发言列表）去详情页
		PartDetail(index){
			this.$router.push({
				path: `/Comment`,
				query:{
					id: this.publishData[index].TieID,
					name: this.name
				}
			})
		},
		//获取token验证,token 不能有" "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	},
    	//返回上一页
		goBack() {
			this.$router.go(-1);
		},
    	//分页跳转
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			//更新路由参数的值
			this.param.set('currentPage',this.activePage);
			this.realGotData();
			//保存页码
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
		},
		//获取富文本内容
		showInfo(e){
			this.pubForm.content = e;
		}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
p{
	margin: 0;
	padding: 0;
}
.course{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 55*@allwidth;
		padding-bottom: 5*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			height: 40*@allwidth;
			line-height: 40*@allwidth;
			padding: 0 20*@allwidth;
			font-size: 20*@allwidth;
		}
		li:last-child{
			margin-left: auto;
			background: #28acc1;
			color: #fff;
			cursor: pointer;
			padding: 0 20*@allwidth;
			border-radius: 5px;
		}
	}
	.TieList{
		width: 95%;
		margin: 10*@allwidth auto;
		height: calc(100vh - 230*@allwidth);
		min-height: 230px;
		overflow-x: hidden;
		overflow-y: auto;
		li{
			display: flex;
			align-items: flex-start;
			padding: 5*@allwidth 0;
			border-bottom: 1px solid #D2D2D2;
			img{
				width: 50*@allwidth;
				height: 50*@allwidth;
				border-radius: 50%;
				margin: 0 15*@allwidth;
			}
			.part{
				cursor: pointer;
				p{
					font-size: 16*@allwidth;
					padding: 4*@allwidth 0;
					color: #406599;
					span{
						opacity: 0.8;
						font-size: 13*@allwidth;
						margin-left: 3px;
					}
					.tags{
						border: 1px solid green;
						font-size: 12*@allwidth;
					}
				}
				p:nth-child(2){
					color: black;
					font-size: 18*@allwidth;
				}
			}
		}
		li:hover{
			background: #ecf5ff;
		}
	}
	.showCourse{
		width: 100%;
		margin-top: 5*@allwidth;
		display: flex;
		flex-wrap: wrap;
		li{
			width: 18%;
			height: 88*@allwidth;
			border: 2px solid #D2D2D2;
			border-radius: 5px;
			margin: 5*@allwidth 0.6%;
			display: flex;
			align-items: center;
			justify-content: center;
			cursor: pointer;
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}

//修改element-ui 的样式
.course /deep/ .el-dialog__body {
    padding: 0;
}
</style>