<template>
  <div class="Head">
  	<div class="log">
  		<img src="../assets/img/log.gif">
  		<img src="../assets/img/text.gif">
  	</div>
  	<div class="exit">
      <i @click="iflogout()" class="el-icon-switch-button"></i>
      <el-badge v-if="newnum > 0"
      :value="newnum" class="item" 
      @click.native="onNews()"
      :max="99" style="cursor: pointer;">
        <i class="el-icon-message"></i>
      </el-badge>
      <i v-else class="el-icon-message"></i>
  	</div>
  </div>
</template>

<script>
export default {
  name: 'Head',
  data(){
  	return {
		  param: new FormData(),
      newnum: 0
  	}
  },
  mounted(){
    if (this.getToken()){
      var that = this;
      setInterval(function(){
        that.getnnews();
      },90000);
    } 
  },
  methods:{
    //是否退出登录
    iflogout(){
      var that = this;
      this.$confirm('确认退出登录？')
      .then(()=>{
        that.LogOut();
      })
      .catch(()=>{})
    },
    getnnews(){
      var that = this;
      var param = new FormData();
      param.set('token',this.getToken());
      this.axios.post(this.$baseUrl + '/unreadNum',param)
      .then((res)=>{
        // console.log(res.data);
        if (res.data.code == 200) {
          that.newnum = res.data.data.numbers;
          console.log(that.newnum);
        }
      })
    },
  	LogOut(){
  		var that = this;
  		var url = this.$baseUrl + '/logout';
  		this.param.set('token',this.getToken());
  		this.axios.post(url,this.param)
  		.then((res)=>{
  			console.log(res.data);
  			if (res.data.code == 200) {
          sessionStorage.removeItem("token");
  				that.$message.success('退出登录！');
          that.$router.push('/');
  			}
  		})
  		.catch((e)=>{
  			console.log(e);
  		})
  	},
  	//获取token
  	getToken(){
  	var theToken = sessionStorage.getItem('token');
  		return theToken.slice(1,theToken.length-1);
  	},
    onNews(){
      this.$router.push('/Mymsg');
      this.newnum = 0;
    }
  }
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.Head {
	width: 100vw;
	height: 57*@allwidth;
	border-bottom: 1px solid #C1E7EB;
	background: linear-gradient(to bottom right,#fff,#2DCB58);
	display: flex;
	align-items: center;
	.log{
		height: 100%;
		display: flex;
		align-items: center;
		img{
			height: 100%;
		}
	}
	.exit{
		margin-left: auto;
		margin-right: 20px;
    width: 90*@allwidth;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    // justify-content: space-between;
		i{
			font-size: 20px;
      cursor: pointer;
      padding: 0 3*@allwidth;
		}
	}
}
</style>
