<template>
	<div class="UpCourseWare">
		<ul class="newsHead">
			<li @click="goUpload()">课程列表</li>
			<li>课件列表</li>
			<li @click="isadd = !isadd">{{isadd? '取消': '上传课件'}}</li>
		</ul>
		<div class="addWare" v-if="isadd">
			<el-upload
				class="upload-demo"
				ref="upload"
				action="#"
				multiple
				accept=".pptx,.ppt,.docx,.doc,.pdf"
				:on-change="handlePPT"
				:on-preview="handlePreview"
				:on-remove="handleRemove"
				:file-list="fileList"
				:auto-upload="false">
				<div class="addBtn" slot="trigger">选取文件</div>
				<div class="addBtn" @click="submitUpload">确定上传</div>
				<div style="border-top: 1px solid black; margin-top: 5px; padding-top: 10px;" slot="tip" class="el-upload__tip">文件预览</div>
			</el-upload>
		</div>
		<div class="PPTlist" v-else>
			<div :style="{display: noData? 'none':'block'}">
				<ul class="PList">
					<li>
						<p>课件名</p>
						<p>上传时间</p>
						<p>下载课件</p>
						<p>删除课件</p>
					</li>
					<li v-for="(item,index) in pptList" :key="index">
						<p>{{item.title}}</p>
						<p>{{item.time}}</p>
						<p><a :href="imgUrl+item.url" :download="item.title"><i class="el-icon-download"></i></a></p>
						<p><i @click="ifDelware(item.id)" class="el-icon-delete"></i></p>
					</li>
				</ul>
				<div class="Page">
					<el-pagination
					  v-if="ispage"
				      @current-change="handleCurrentChange"
				      :current-page.sync="activePage"
				      :page-size="8"
				      :pager-count="5"
				      layout="prev, pager, next"
				      :total="totalPage">
				    </el-pagination>
				</div>
			</div>
			<div :style="{display: noData? 'block':'none'}">
		  		<div class="notice">
			  		<img src="../assets/img/nodata.png">
			  		<p>暂无内容！</p>
			  	</div>
		  	</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'UpCourseWare',
	data(){
		return{
			imgUrl: '',
			param: new FormData(),
			courseID: '',
			totalPage: 1,
			activePage: 1,
			url: '',
			isadd: false,
			fileList:[],
			name: '课件',
			pptList: [],
			ispage: false,
			noData: true
		}
	},
	mounted(){
		if (this.getToken()) {
			this.addWareParam();
			this.imgUrl = this.$ImgUrl + '/courseware/'; 
		}
	},
	methods: {
		//上传课件
		submitUpload() {
			var that = this;
			var uParam = new FormData();
			for(let key of this.fileList) {
				uParam.set('multipartFile',key.raw);
			}
			uParam.set('superiorId',this.courseID);
			uParam.set('token',this.getToken());
			this.axios.post(this.$baseUrl+'/uploadCourseware',uParam)
			.then((res)=>{
				console.log(res);
				if (res.data.code == 200) {
					that.$message.success('上传成功！');
					this.isadd = false;
					that.viewWare();
				} else {
					// that.$message.error('上传失败!');
					that.$message.error('请选择PPT格式文件！')
				}
			})
			.catch((e)=>{
				console.log(e)
			})
		},
		//取消文件
		handleRemove(file, fileList) {
			console.log(file, fileList);
		},
		handlePreview(file) {
			console.log(file);
		},
		//监听选择的文件
		handlePPT(file,fileList){
			// console.log(fileList);
			this.fileList = fileList;
		},
		//切换页面
		goUpload(){
			this.$router.push('/CourseUpload');
		},
		//查看课件添加参数
		addWareParam(){
			var nowPage = sessionStorage.getItem('nowPage');
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.url = this.$baseUrl + '/coursewares';
			this.courseID = sessionStorage.getItem('courseID');
			this.param.set('id',this.courseID);
			this.param.set('token',this.getToken());
			this.param.set('pageNum',8);
			this.param.set('currentPage',this.activePage);
			this.viewWare();
		},
		//查看课件具体函数
		viewWare(){
			var that = this;
			this.axios.post(this.url,this.param)
			.then((res)=>{
				// console.log(res.data);
				var res = res.data;
				var theJSON = {};
		        that.pptList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		title: perData.title,
		        		url: perData.url,
		        		time: perData.time
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	console.log(res.data)
		        	that.totalPage = res.data.totalNumber;
		        	console.log(Array.isArray(res.data.pageData))
		        	if (Array.isArray(res.data.pageData)) {
		        		for(var key of res.data.pageData){
			        		that.pptList.push(formatData(key));
			        	}
		        		that.noData = false;
		        	} else {
		        		that.$message.warning('暂无课件！');
		        	}
		        	if (res.data.totalNumber > 8) {
			        	that.ispage = true;
			        }  else {
		        		that.ispage = false;
		        	}
		        } else {
		        	that.$message.error('获取'+ that.name +'列表失败！');
		        }
			})
		},
		//获取token
		getToken(){
		var theToken = sessionStorage.getItem('token');
			return theToken.slice(1,theToken.length-1);
		},
		//获取当前页信息
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			//实时更新路由，避免每次返回都是page=1
			this.param.delete('currentPage');
			this.param.set('currentPage',this.activePage);
			this.viewWare();
		},
		//是否删除课件
		ifDelware(id){
			var that = this;
			this.$confirm('确认删除该条信息？')
			.then(()=>{
				that.DoDel(id);
			})
			.catch(()=>{})
		},
		DoDel(id){
			var that = this;
			var url = this.$baseUrl + '/courseware/delete/' + id;
			var Dparam = new FormData();
			Dparam.set('token',this.getToken());
			this.axios.post(url,Dparam)
			.then((res)=>{
				console.log(res.data);
				if (res.data.code == 200) {
					that.viewWare();
					that.$message.success('删除成功！');
				} else {
					that.$message.error(res.data.message);
				}
			})
			.catch(()=>{})
		},
    }
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
p{
	margin: 0;
	padding: 0;
}
.UpCourseWare{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 70*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			height: 45*@allwidth;
			line-height: 45*@allwidth;
			padding: 0 15*@allwidth;
			color: #fff;
			background: #099c4a;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 20*@allwidth;
		}
		li:first-child{
			background: #90a7a6;
		}
		li:nth-child(3){
			margin-left: auto;
			background: #28acc1;
		}
	}
	.addWare{
		width: 80%;
		margin: 0 auto;
		margin-top: 20*@allwidth;
		.addBtn{
			float: right;
			text-align: center;
			height: 38*@allwidth;
			line-height: 38*@allwidth;
			padding: 0 12*@allwidth;
			color: #fff;
			background: #348ce6;
			cursor: pointer;
			border-radius: 5px;
			font-size: 17*@allwidth;
		}
	}
	.PPTlist{
		width: 100%;
		.PList{
			margin: 10*@allwidth 0;
			li{
				display: flex;
				line-height: 40*@allwidth;
				font-size: 18*@allwidth;
				p:first-child{
					width: 55%;
					text-align: left;
					padding-left: 20*@allwidth;
				}
				p{
					text-align: center;
					width: 10%;
					a{
						color: black;
						i{
							font-size: 22*@allwidth;
							font-weight: bold;
							cursor: pointer;
						}
						i:hover{
							color: #28acc1;
						}
					}
					i:hover{
						color: red;
					}
				}
				p:nth-child(2){
					width: 25%;
					
				}
			}
			li:first-child{
				background: #3c5d80;
				color: #fff;
				p{
					line-height: 40*@allwidth;
				}
			}
			li:first-child:hover{
				background: #3c5d80;
			}
			li:hover{
				background: #ecf5ff;
			}
		}
		.Page{
			margin-top: 15*@allwidth;
			.el-pagination{
				float: right;
				font-weight: normal;
			}
		}
	}
}
.notice{
	width: 99%;
	margin: 10*@allwidth auto;
	height: calc(100vh - 190*@allwidth);
	min-height: 200px;
	box-shadow: 0 0 0.3px 0.3px #5D8486;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	img{
		width: 70px;
		height: 70px;
	}
	p{
		font-size: 18*@allwidth;
		margin: 5*@allwidth;
		color: #5D8486;
	}
}
</style>