<template>
	<div class="publish">
		<div class="pubHead">
			<i class="el-icon-back" @click="goBack()"></i>
			<p>{{pubName}}</p>
			<div class="annouce">
				<div @click="publishing()" class="selectStu">{{pubBtn}}</div>
			</div>
		</div>
		<hr>
		<div class="editor">
			<div class="title">
				<p class="intro">标题</p>
				<input v-model="title" type="text" :maxlength="15" name="search" placeholder="请输入标题（ 15 字内 ）"  autocomplete="off">
				<div class="TBtn" v-if="name == 'test' || name == 'homework' ? true:false">
					<span @click="dialogVisible = true">设置起止时间</span>
					<span>{{startTime}}</span>-<span>{{endTime}}</span>
				</div>
				<div class="TBtn" v-if="(name == 'Retest' || name == 'Rehomework' ) && oldload ? true:false">
					<span @click="dialogVisible = true">设置起止时间</span>
					<span>{{DateTranse(startTime)}}</span>-<span>{{DateTranse(endTime)}}</span>
				</div>
			</div>
			<div class="contain">
				<p class="intro">内容</p>
				<div class="rest">
					<Edit @newsEvent="showInfo" :Tcontent="content" :key="old.infoID"></Edit>
				</div>
			</div>
		</div>
		<el-dialog
		  title="设置起止时间"
		  :visible.sync="dialogVisible"
		  width="50%">
		  <div class="block">
			    <el-date-picker
			      v-model="deadline"
			      type="datetimerange"
			      align="right"
			      start-placeholder="开始日期"
			      end-placeholder="结束日期"
			      :default-time="['08:00:00', '08:00:00']">
			    </el-date-picker>
		  </div>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="dialogVisible = false">取 消</el-button>
		    <el-button type="primary" @click="getDeadline">确 定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
import Edit from '../components/ue.vue';
export default {
	name: 'publish',
	components:{
		Edit
	},
	data(){
		return {
			param: new FormData(),
			courseID: '',
			deadline: '',
			pubName: '',
			pubBtn: '',
			title: '',
			content: '',
			name: '',
			startTime: '未选择',
			endTime: '未选择',
			dialogVisible: false,
			url: '',
			old:{
				title: '',
				content: '',
				publisher: '',
				infoID: '',
				startTime: '',
				endTime: ''
			},
			oldload: false
		}
	},
	mounted(){
		if (this.getToken()) {
			// this.param.
			var ifRe = this.$route.query.name.indexOf('Re');
			ifRe == 0 ? this.correctInfo():this.pubGetName();
		}
	},
	methods:{
		//获取发布类别
		pubGetName(){
			var query = this.$route.query;
			this.name = query.name;
			this.pubBtn = '发布';
			this.courseID = sessionStorage.getItem('courseID');
			this.param.set('token',this.getToken());
			switch(this.name) {
				case 'news':
					this.pubName = '发布新闻';
					this.url = this.$baseUrl + '/article/add';
					break;
				case 'inform':
					this.pubName = '发布通知';
					this.param.set('courseId',this.courseID);
					this.url = this.$baseUrl + '/pNotice';
					break;
				case 'test':
					this.pubName = '发布考试';
					this.param.set('courseId',this.courseID);
					this.url = this.$baseUrl + '/homework/pub';
					break;
				case 'homework':
					this.pubName = '发布作业';
					this.param.set('courseId',this.courseID);
					this.url = this.$baseUrl + '/homework/pub';
					break;
				default :
					break;
			}
		},
		//修改内容
		correctInfo(){
			var that = this;
			var query = this.$route.query;
			var OldParam = new FormData();
			OldParam.set('id',query.id);
			OldParam.set('token',this.getToken());
			this.name = query.name;
			console.log(query.name)
			var oldUrl = '';
			this.pubBtn = '确定';
			switch(this.name){
				case 'Renews':
					this.pubName = '修改新闻';
					this.url = this.$baseUrl + '/article/update';
					oldUrl = this.$baseUrl + '/article/' + query.id;
					break;
				case 'Retest':
					this.pubName = '修改考试';
					this.url = this.$baseUrl + '/homework/update';
					oldUrl = this.$baseUrl + '/homework/' + query.id;
					break;
				case 'Rehomework':
					this.pubName = '修改作业';
					this.url = this.$baseUrl + '/homework/update';
					oldUrl = this.$baseUrl + '/homework/' + query.id;
					break;
				default :
					break;
			}
			//获取之前内容
			// this.axios.post(oldUrl,query.theParam)
			this.axios.post(oldUrl,OldParam)
			.then((res)=>{
				var that = this;
				// console.log(res.data);
				if (res.data.code == 200) {
					let data = res.data.data;
					that.old.title = data.title;
					that.old.content = data.content;
					that.old.publisher = data.publisherId;
					that.old.infoID = data.id;
					if (this.name == 'Retest' || this.name == 'Rehomework') {
						that.oldload = true;
						that.old.startTime = data.startTime;
						that.old.endTime = data.endTime;
						that.startTime = data.startTime;
						that.endTime = data.endTime;
					}
					//将历史值展示出来
					that.title = data.title;
					that.content = data.content;
				} else {
					this.$message.error('获取内容失败！');
				}
			})
		},
		//校验信息，然后发布信息
		publishing(){
			if (this.title == '') {
				this.$message.error('发布标题不能为空！');
				return ;
			}
			if( this.content == '') {
				this.$message.error('发布内容不能为空！');
				return ;
			}
			console.log(this.name)
			if (this.name == 'test' || this.name == 'homework') {
				if (this.startTime == '未选择') {
					this.$message.error('请设置起止时间！');
					return ;
				} else {
					this.param.set('startTime',this.startTime);
					this.param.set('endTime',this.endTime);
					this.param.set('category',this.name);
				}
			}
			if (this.pubBtn == '确定') {
				if ((this.title == this.old.title) && (this.content == this.old.content)) {
					this.$message.error('请确定有修改内容！');
				} else {
					this.corNews();
				}
			} else {
				this.pubNews();
			}
		},
		//发布新闻
		pubNews(){
			var that = this;
			this.param.set('content',this.content);
			if (this.name == 'news') {
				var fimg = this.imgUrlFun(this.content);
				if (fimg) {
					this.param.set('image',fimg);
				}
				this.param.set('brief',this.filterHTMLTag(this.content));
			}
			this.param.set('title',this.title);
			this.axios.post(this.url,this.param)
			.then((response)=>{
				var res = response.data;
				if (res.code == 200) {
					that.$message.success('发布成功！');
					that.goBack();
				} else {
					that.$message.error('发布失败！');
				}
			})
			.catch((e)=>{
				that.$message.error(e);
			})
		},
		//修改新闻
		corNews(){
			var that =this;
			this.param.set('content',this.content);
			if (this.name == 'Renews') {
				var fimg = this.imgUrlFun(this.content);
				if (fimg) {
					this.param.set('image',fimg);
				}
				this.param.set('brief',this.filterHTMLTag(this.content));
			}
			this.param.set('title',this.title);
			this.param.set('token',this.getToken());
			this.param.set('publisherId',this.old.publisher);
			this.param.set('id',this.old.infoID);
			this.axios.post(this.url,this.param)
			.then((response)=>{
				var res = response.data;
				if (res.code == 200) {
					that.$message.success('修改成功！');
					that.goBack();
				} else {
					that.$message.error('修改失败！');
				}
			})
			.catch((e)=>{
				that.$message.error(e);
			})
		},
		//获取历史内容
		getOldInfo(){
			var that =this;
			this.axios.post(this.$baseUrl + '/pNotice',{
				content: this.content,
				title: this.title,
			})
			.then((response)=>{
				var res = response.data;
				res.code == 200 ? that.$message.success('发布成功！'):that.$message.error('发布失败！');
			})
			.catch((e)=>{
				that.$message.error(e);
			})
		},
		//获取起止时间
		getDeadline(){
			console.log(this.deadline);
			this.startTime = this.DateTranse(this.deadline[0]);
			this.endTime = this.DateTranse(this.deadline[1]);
			this.dialogVisible = false;
		},
		//转换日期格式
		DateTranse(data){
			var d = new Date(data);
			var hour = d.getHours() < 10 ? '0'+ d.getHours():d.getHours();
			var minu = d.getMinutes() < 10 ? '0'+ d.getMinutes():d.getMinutes();
			var sec = d.getSeconds() < 10 ? '0'+ d.getSeconds():d.getSeconds();
			var datetime=d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + hour + ':' + minu + ':' + sec;
			return datetime;
		},
		//返回上一页
		goBack() {
			this.$router.go(-1);
		},
		//获取富文本内容
		showInfo(e){
			this.content = e;
		},
		//获取token
		getToken(){
			var theToken = sessionStorage.getItem('token');
			return theToken.slice(1,theToken.length-1);
		},
		//获取富文本里的第一张图片
	    imgUrlFun(str){
	        var data = '';
            str.replace(/<img [^>]*src=['"]([^'"]+)[^>]*>/, function (match, capture) {
                  data =  capture;
            });
            // console.log(data);
	        return data;
	    },
	    //获取内容简介(前30个字)
	    filterHTMLTag(str) {
	        str = str.replace(/<\/?[^>]*>/g,''); //去除HTML tag
	        str = str.replace(/[ | ]*\n/g,'\n'); //去除行尾空白
	        str = str.replace(/ /ig,'');//去掉 
	        console.log(str)
	        var endlen = str.length >= 30 ? 30 : str.length;
	        var desc = str.slice(0,endlen);
	        console.log(desc,desc.length)
	        return desc;
		}
	}
}
</script>

<style lang="less">
@allwidth: 100/1386vw;
.publish{
	width: 95%;
	margin: 0 auto;
	hr{
		border: 0.8px solid rgb(61, 126, 141);
		margin: 0;
	}
	.pubHead{
		height: 65*@allwidth;	
		font-size: 22*@allwidth;
		display: flex;
		align-items: center;
		i{
			font-weight: bold;
		}
		p{
			padding-left: 40*@allwidth;
		}
		.annouce{
			margin-left: auto;
			display: flex;
			.selectStu{
				text-align: center;
				height: 45*@allwidth;
				line-height: 45*@allwidth;
				width: 100*@allwidth;
				color: #fff;
				background: #2b8290;
				cursor: pointer;
				margin: 0 3*@allwidth;
				border-radius: 5px;
				font-size: 20*@allwidth;
			}
		}
	}
	.editor{
		margin-top: 15*@allwidth;
		.title{
			display: flex;
			align-items: center;
			p{
				font-size: 22*@allwidth;
				width: 60*@allwidth;
				min-width: 35px;
				margin: 0 10px;
			}
			input{
				border: 1px solid #D2D2D2;
				width: 40%;
				height: 40*@allwidth;
				color: #666;
				line-height: 40*@allwidth;
				padding: 0 10*@allwidth;
				font-size: 18*@allwidth;
				outline: none;
			}
			input:hover{
				border-color: #409EFF;
			}
			.TBtn{
				margin-left: 3%;
				display: flex;
				align-items: center;
				span{
					margin: 0 10*@allwidth;
				}
				span:first-child{
					color: #409EFF;
					line-height: 40*@allwidth;
					padding: 0 10*@allwidth;
					font-size: 18*@allwidth;
					background: #ecf5ff;
					border-radius: 10*@allwidth;
					cursor: pointer;
				}
				
			}
		}
		.contain{
			width: 100%;
			display: flex;
			margin-top: 10*@allwidth;
			.intro{
				font-size: 22*@allwidth;
				width: 60*@allwidth;
				margin: 0 10px;
			}
			.rest{
				width: 100%;
			}
		}
	}
}
.selfdialog{
	.el-dialog__body{
		padding-top: 0;
	}
}
</style>