<template>
  <div class="news">
  	<div class="newsHead">
  		<ul class="tabs">
  			<li :style="category=='作业'? '':{background: '#90a7a6'}" @click="$router.push('/Exam/homework')">作业管理</li>
  			<li :style="category=='考试'? '':{background: '#90a7a6'}" @click="$router.push('/Exam/test')">考试管理</li>
  		</ul>
  		<ul class="add">
  			<li @click="publish()">发布{{category}}</li>
  		</ul>
  	</div>
  	<div :style="{display: noData? 'none':'block'}">
  		<div class="showList">
	  		<el-row v-for="(list,index) in showList" :key="index">
			  <el-col :span="22" @click.native="showWork(list.id)">
			  	<div class="main">
			  		<p>{{list.title}}</p>
			  		<p>作业时间： {{list.startTime}} — {{list.endTime}}</p>
			  	</div>
			  	<div class="graded">评分</div>
			  </el-col>
			  <el-col :span="2">
			  	<div class="operate">
			  		<i @click="Tdetails(list.id)" class="el-icon-document" title="详情"></i>
			  		<i @click="TDelete(list.id)" class="el-icon-delete" title="删除"></i>
			  	</div>
			  </el-col>
			</el-row>
	  	</div>
	  	<div class="paging">
	  		<el-pagination
	  		  v-if="ispage"
		      @current-change="handleCurrentChange"
		      :current-page.sync="activePage"
		      :page-size="5"
		      :pager-count="5"
		      layout="prev, pager, next"
		      :total="totalPage">
		    </el-pagination>
	  	</div>
  	</div>
  	<div :style="{display: noData? 'block':'none'}">
  		<div class="notice">
	  		<img src="../assets/img/nodata.png">
	  		<p>暂无内容！</p>
	  	</div>
  	</div>
  </div>
</template>

<script>
export default{
	name: 'news',
	data() {
      return {
		param: new FormData(),
      	activePage: 1,
      	totalPage: 1, 
      	category: '',
      	name: '',
      	url: '',
        showList:[],
        ispage: false,
        noData: true
      };
    },
    mounted(){
    	if (this.getToken()) {
			this.getListParam();
		}
    },
    methods: {
    	getListParam(){
			var courseID = sessionStorage.getItem('courseID');
			this.url = this.$baseUrl + '/homework/show';
    		let name = this.$route.params.name;
			if (name == 'homework') {
				this.category = '作业';
			} else {
				this.category = '考试';
			}
			this.name = name;
			var nowPage = sessionStorage.getItem('nowPage');
			console.log(nowPage)
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.set('courseId',courseID);
    		this.param.set('category',name);
    		this.param.set('token',this.getToken());
			this.param.set('pageNum',6);
			this.param.set('currentPage',this.activePage);
			this.getListData();
    	},
    	getListData(){
    		var that = this;
    		this.axios.post(this.url,this.param)
    		.then((res)=>{
    			res = res.data;
    			console.log(res);
    			var theJSON = {};
		        that.showList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		title: perData.title,
		        		publisher: perData.publisherId,
		        		// startTime: perData.startTime,
		        		// endTime: perData.endTime
		        		endTime: that.DateTranse(perData.endTime),
		        		startTime: that.DateTranse(perData.startTime)
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	console.log(res.data);
		        	that.totalPage = res.data.totalNumber;
		        	if (Array.isArray(res.data.pageData)) {
		        		for(var key of res.data.pageData){
			        		that.showList.push(formatData(key));
			        	}
		        		that.noData = false;
		        	} else {
		        		that.$message.warning('暂无数据！');
		        	}
		        	if (res.data.totalNumber > 6) {
		        		that.ispage = true;
		        	} else {
		        		that.ispage = false;
		        	}
		        } else {
		        	that.$message.error('获取'+ that.name +'列表失败！');
		        }
    		})
    	},
    	//转换日期格式
		DateTranse(data){
			var d = new Date(data);
			var hour = d.getHours() < 10 ? '0'+ d.getHours():d.getHours();
			var minu = d.getMinutes() < 10 ? '0'+ d.getMinutes():d.getMinutes();
			var sec = d.getSeconds() < 10 ? '0'+ d.getSeconds():d.getSeconds();
			var datetime=d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + hour + ':' + minu + ':' + sec;
			return datetime;
		},
    	//分页效果
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			this.param.set('currentPage',this.activePage);
			this.getListData();
			//保存页码
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
		},
		//进入作业/考试详情页
		Tdetails(id){
			this.$router.push({
				path: `/Show`,
				query:{
					name: this.name,
					id: id
				}
			});
		},
		TDelete(id){
			var that = this;
			this.$confirm('确认删除该条作业？')
			.then(()=>{
				that.deleteList(id)
			})
			.catch(()=>{})
		},
		// 删除该条信息
		deleteList(id){
			var that = this;
			var Dparam = new FormData();
			var DdataUrl = this.$baseUrl + '/homework/delete';
			Dparam.set('id',id);
			Dparam.set('token',this.getToken());
			this.axios.post(DdataUrl,Dparam)
			.then((res)=>{
				console.log(res)
				if (res.data.code == 200) {
					this.getListData();
					that.$message.success('删除成功！');
				} else {
					that.$message.error('删除失败！');
				}
			})
		},
		//发布作业/考试
		publish(){
			this.$router.push({
	          path: `/Publish`,
				query:{
					name: this.name,
					// id: 
				}
	        })
		},
		//学生完成作业详情
		showWork(id){
			console.log(id)
			this.$router.push({
				path: `/Homework`,
				query:{
					name: this.name,
					id: id,
					// page: 1
				}
			})
		},
		//获取token
		getToken(){
		var theToken = sessionStorage.getItem('token');
			return theToken.slice(1,theToken.length-1);
		}
    }
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
p{
	margin: 0;
	padding: 0;
}
.news{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 70*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			height: 45*@allwidth;
			width: 100*@allwidth;
			color: #fff;
			background: #099c4a;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 20*@allwidth;
		}
		.tabs{
			display: flex;
			line-height: 45*@allwidth;
		}
		.add{
			margin-left: auto;
			line-height: 45*@allwidth;
			li{
				background: #28acc1;
			}
		}
	}
	.showList{
		padding-top: 18*@allwidth;
		.el-row{
			height: 55*@allwidth;
			border: 1px solid grey;
			border-radius: 5px;
			font-size: 14*@allwidth;
			padding: 0 20*@allwidth;
			margin-bottom: 4*@allwidth;
			color: #999999;
			display: flex;
			.el-col{
				display: flex;
				align-items: center;
				cursor: pointer;
				.main{
					display: flex;
					align-items: center;
					margin: 5*@allwidth 0;
					p{
						font-size: 18*@allwidth;
						color: #656565;
					}
					p:nth-child(2){
						font-size: 14*@allwidth;
						color: #999999;
						opacity: 0.7;
						margin-left: 30*@allwidth;
					}
				}
				.graded{
					margin-left: auto;
					opacity: 1;
					height: 40*@allwidth;
					line-height: 40*@allwidth;
					text-align: center;
					width: 40*@allwidth;
					border-radius: 50%;
					background: grey;
					color: #fff;
					font-size: 14*@allwidth;
				}
			}
			.operate{
				font-size: 25*@allwidth;
				margin-left: 16*@allwidth;
				i{
					padding: 0 4*@allwidth;
				}
				i:hover{
					color: #98E26E;
				}
			}
		}
		.el-row:hover{
			border: 2px solid #58CD77;
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}
.notice{
	width: 99%;
	margin: 10*@allwidth auto;
	height: calc(100vh - 190*@allwidth);
	min-height: 200px;
	box-shadow: 0 0 0.3px 0.3px #5D8486;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	img{
		width: 70px;
		height: 70px;
	}
	p{
		font-size: 18*@allwidth;
		margin: 5*@allwidth;
		color: #5D8486;
	}
}
</style>