<template>
	<div class="course">
		<ul class="newsHead">
			<li>已有课程</li>
			<li @click="addCourse">创建课程</li>
		</ul>
		<div :style="{display: noData? 'none':'block'}">
			<ul class="showCourse">
				<li v-for="(item,index) in courseList" :key="item.id" class="courseList">
					<img :src="imgUrl + item.cover" alt="123" @click="goCourse(item.id,item.name)">
					<ul class="info">
						<li @click="goCourse(item.id,item.name)">{{item.name}}</li>
						<li>课程码：{{item.code}}</li>
						<li>
							<p>讲师：{{item.teacher}}</p>
							<div class="change">
								<i @click="ifClose(item.id)" class="el-icon-remove-outline" title="关闭课程"></i>
								<i @click="UpCourse(index)" class="el-icon-edit-outline" title="修改课程"></i>
								<i @click="ifDelCourse(item.id)" class="el-icon-delete" title="删除课程"></i>
							</div>
						</li>
					</ul>
				</li>
			</ul>
			<div class="paging">
		  	 <!-- ref="pagination" -->
		  		<el-pagination
		  		  v-if='ispage'
			      @current-change="handleCurrentChange"
			      :current-page.sync="activePage"
			      :page-size="8"
			      :pager-count="5"
			      layout="prev, pager, next"
			      :total="totalPage">
			    </el-pagination>
		  	</div>
		</div>
		<div :style="{display: noData? 'block':'none'}">
	  		<div class="notice">
		  		<img src="../assets/img/nodata.png">
		  		<p>暂无内容！</p>
		  	</div>
	  	</div>
	</div>
</template>

<script>
export default {
	name: 'course',
	data(){
		return {
			imgUrl: '',
			param: new FormData(),
			courseList: [],
			ispage: false,
			activePage: 1,
			totalPage: 1,
			noData: true
		}
	},
	mounted(){
		if (this.getToken()) {
			this.getParamInfo();
			this.imgUrl = this.$ImgUrl + '/course/';
		}
	},
	methods:{
		//获取当前页信息
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			this.param.set('currentPage',this.activePage);
			this.getCourseList();
			//保存页码
			sessionStorage.setItem('nowPage', JSON.stringify(val));
		},
		getParamInfo(){
			//获取当前页
			var nowPage = sessionStorage.getItem('nowPage');
			console.log(nowPage)
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.set('currentPage',this.activePage);
			this.param.set('pageNum',9);
			this.param.set('token',this.getToken());
			this.getCourseList();
		},
		//获取课程列表
		getCourseList(){
			var that = this;
			// this.axios.post(this.$baseUrl + '/courses',this.param)
			this.axios.post(this.$baseUrl + '/courses',this.param)
			.then((res)=>{
				console.log(res.data);
				var theJSON = {};
				that.courseList = [];
				var formatData = function(perData){
					theJSON = {
						id: perData.id,
						teacher: perData.teaName,
						name: perData.title,
						cover: perData.url,
						desc: perData.description,
						code: perData.code
					}
					return theJSON;
				}
				if (res.data.code == 200) {
					that.totalPage = res.data.data.totalNumber;
					var data = res.data.data.pageData;
					if (Array.isArray(data)) {
						for(let key of data){
							that.courseList.push(formatData(key));
						}
						that.noData = false;
					} else {
						that.$message.warning( '课程列表为空！');
					}
					if (res.data.data.totalNumber > 9) {
						that.ispage = true;
					}  else {
		        		that.ispage = false;
		        	}
				} else {
					that.$message.error('获取课程列表失败！');
				}
			})
			.catch((e)=>{
				that.$message.error(e);
			})
		},
		//获取token验证,token 不能有" "
		getToken(){
			var theToken = sessionStorage.getItem('token');
			return theToken.slice(1,theToken.length-1);
		},
		//前往课程具体内容
		goCourse(id,name) {
			sessionStorage.setItem('courseID', JSON.stringify(id));
			sessionStorage.setItem('courseName', JSON.stringify(name));
			this.$router.push('/News/inform');
		},
		//添加课程
		addCourse(){
			this.$router.push('/CourseAdd');
		},
		//修改课程
		UpCourse(index){
			this.$router.push({
				path: `/CourseAdd`,
				query:{
					id: this.courseList[index].id,
					name: this.courseList[index].name,
					desc: this.courseList[index].desc,
					cover: this.courseList[index].cover,
					code: this.courseList[index].code
				}
			});
		},
		//关闭课程
		ifClose(id){
			var that = this;
			this.$confirm('确认关闭该课程？')
			.then(()=>{
				that.closeList(id)
			})
			.catch(()=>{})
		},
		//关闭该课程
		closeList(id){
			var that = this;
			var Dparam = new FormData();
			var DdataUrl =  this.$baseUrl + '/course/over';
			Dparam.set('token',this.getToken());
			Dparam.set('courseId',id)
			// Dparam.set('')
			this.axios.post(DdataUrl,Dparam)
			.then((res)=>{
				if (res.data.code == 200) {
					this.getCourseList();
					that.$message.success('关闭成功！');
				} else {
					that.$message.error('关闭失败！');
					// that.$message.error(res.data.message);
				}
			})
		},
		//是否确定删除
		ifDelCourse(id){
			var that = this;
			this.$confirm('确认删除该条信息？')
			.then(()=>{
				that.deleteList(id)
			})
			.catch(()=>{})
		},
		// 删除该课程
		deleteList(id){
			var that = this;
			var Dparam = new FormData();
			var DdataUrl =  this.$baseUrl + '/course/delete/' + id;
			Dparam.set('token',this.getToken());
			// Dparam.set('')
			this.axios.post(DdataUrl,Dparam)
			.then((res)=>{
				if (res.data.code == 200) {
					this.getCourseList();
					that.$message.success('删除成功！');
				} else {
					that.$message.error('删除失败！');
				}
			})
		}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
.course{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 55*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			min-width: 65*@allwidth;
			height: 40*@allwidth;
			line-height: 40*@allwidth;
			padding: 0 10*@allwidth;
			margin: 0 3*@allwidth;
			font-size: 20*@allwidth;
			color: #fff;
			background: #099c4a;
			border-radius: 5px;
		}
		li:last-child{
			margin-left: auto;
			background: #28acc1;
			cursor: pointer;
		}
	}
	.showCourse{
		width: 100%;
		margin-top: 5*@allwidth;
		display: flex;
		flex-wrap: wrap;
		.courseList{
			width: 32%;
			height: 95*@allwidth;
			border: 2px solid #D2D2D2;
			border-radius: 5px;
			margin: 5*@allwidth 0.3%;
			display: flex;
			align-items: center;
			justify-content: center;
			cursor: pointer;
			img{
				width: 30%;
				height: 85%;
				border-radius: 5px;
				margin: 0;
			}
			.info{
				width: 60%;
				height: 75%;
				margin-left: 10*@allwidth;
				position: relative;
				li{
					width: 100%;
					font-size: 18*@allwidth;
					font-weight: bold;
					margin-bottom: 3*@allwidth;
					p{
						font-weight: normal;
						font-size: 14*@allwidth;
					}
				}
				li:nth-child(2){
					font-size: 15*@allwidth;
					font-weight: normal;
				}
				li:last-child{
					margin-top: 7*@allwidth;
					font-size: 17*@allwidth;
					display: flex;
					justify-content: space-between;
					p{
						padding: 0;
						margin: 0;
					}
					.change{
						opacity: 0;
						i{
							margin: 0 1px;
						}
						i:hover{
							color: #59CCA8;
						}
					}
				}
				li:last-child:hover{
					.change{
						opacity: 1;
					}
				}
			}
		}
	}
}
.paging{
	margin-top: 15*@allwidth;
	.el-pagination{
		float: right;
		font-weight: normal;
	}
}
.notice{
	width: 99%;
	margin: 10*@allwidth auto;
	height: calc(100vh - 190*@allwidth);
	min-height: 200px;
	box-shadow: 0 0 0.3px 0.3px #5D8486;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	img{
		width: 70px;
		height: 70px;
	}
	p{
		font-size: 18*@allwidth;
		margin: 5*@allwidth;
		color: #5D8486;
	}
}
</style>