<template>
  <div class="Login">
    <div class="log">
      <img src="../assets/img/log.gif">
      <img src="../assets/img/text.gif">
    </div>
  	<div class="main">
      <p>登录</p>
  		<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="60px"label-position="left" class="demo-ruleForm">
	  		<el-form-item label="账号" prop="mname">
	  			<el-input type="mname" v-model="ruleForm.mname" :maxlength="15" autocomplete="off" placeholder="请输入账户名" oninput="value=value.replace(/[^\d]/g,'')"></el-input>
	  		</el-form-item>
	  		<el-form-item label="密码" prop="password">
	  			<el-input type="password" :maxlength="15" v-model="ruleForm.password" autocomplete="off" placeholder="请输入密码"></el-input>
	  		</el-form-item>
	  		<el-form-item>
	  			<el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
		    	<el-button @click="resetForm('ruleForm')">重置</el-button>
	  		</el-form-item>
		</el-form>
  	</div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Login',
  data(){
    var validatePwd = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('密码不能为空'));
        } else if (value.length < 6) {
          callback(new Error('密码不能少于6位！'));
        }  else {
          callback();
        }
    };
    return {
     	ruleForm: {
        mname: '',
        password: '',
      },
      rules: {
        password: [
          { validator: validatePwd, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    //提交表单
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          //确定有输入数据
          // console.log(valid);
          this.getPermit();
        } else {
          this.$message.error('账号名或密码错误！');
          return false;
        }
      });
    },
    //重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //验证用户身份信息
    getPermit(){
      var that = this;
      let param = new FormData();
      if (this.ruleForm.mname.length < 1) {
        this.$message.error('账号不能为空！');
        return ;
      } else {
        param.set('username',this.ruleForm.mname);
      }
      param.set('isTeacher',1);
      if (this.ruleForm.password.length < 6) {
        this.$message.error('密码不能少于6位！');
        return ;
      } else {
        param.set('password',this.ruleForm.password);
      }
      // this.saveUserInfo('add the token');
      this.$axios.post(this.$baseUrl + '/login',param)
      .then((res)=>{
        console.log(res.data);
        res.data.code == 200? that.saveUserInfo(res.data.data.token):that.$message.error(res.data.message);
      })
      .catch((e)=>{
        console.log(e);
      })
    },
    // 保存用户信息
    saveUserInfo(data){
      let userInfo = {
        name: this.ruleForm.mname,
        psd: this.ruleForm.password
      };
      // console.log(data)
      sessionStorage.setItem('token', JSON.stringify(data));
      // sessionStorage.setItem('test', JSON.stringify('hello world'));
      sessionStorage.setItem('userInfo', JSON.stringify(userInfo));
      this.$message.success('登录成功！');
      this.$router.push('/News/news');
      this.getInfo(data);
    },
    //获取用户信息
    getInfo(token){
      var that = this;
      var url = this.$baseUrl + '/info';
      var param = new FormData();
      param.set('token',token);
      this.axios.post(url,param)
      .then((res)=>{
        res = res.data;
        console.log(res.data);
        if (res.code == 200) {
          var random = Math.random();
          console.log(random)
          // that.$store.commit('upUserInfo',{
          //   name: res.data.name,
          //   cover:res.data.avatar,
          //   random: random
          // })
          that.$store.commit('name',res.data.name);
          that.$store.commit('cover',res.data.avatar);
          that.$store.commit('random',Math.random());
        }
      })
      .catch((e)=>{
        console.log(e);
      })
    },
  }
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.Login{
	width: 100vw;
	height: 100vh;
	background-image: url('../assets/img/Loginback.gif');
	background-size: cover;
	display: flex;
	justify-content: center;
	align-items: center;
  .log{
    height: 70*@allwidth;
    position: absolute;
    top: 10*@allwidth;
    left: 30*@allwidth;
    img{
      height: 100%;
    }
  }
	.main{
		width: 25%;
		min-width: 230px;
		padding: 30px 40px 10px 40px;
		background: #fff;
		border-radius: 10px;
		box-shadow: 0px 0px 5px #3F838D;
    p{
      font-size: 20px;
      text-align: center;
      padding-bottom: 10px;
      opacity: 0.6;
      // color: #47AD80;
      font-weight: bold;
    }
	}
}
</style>
