<template>
  <div id="app">
  	<Header v-if="ifheader"/>
  	<div v-bind:class="{content: ifheader}">
  		<navBar :showPart="showpart" :showIndex="showIndex" v-if="ifheader" :key="showpart"/>
  		<div id="main" ref="main" :key="$route.path">
  			<router-view/>
  		</div>
  	</div>
  </div>
</template>

<script>
import Header from './components/header.vue'
import navBar from './components/navBar.vue'

export default{
	name: 'app',
	components: {
		Header,
		navBar
	},
	data(){
		return {
			ifheader: false,
      showpart:'homeTab',
      showIndex: '',
      sureIndex: '',
		}
	},
	mounted() {
    	this.judgeWidth();
      window.addEventListener('unload', this.saveState)
  	},
    watch: {
        '$route'(to, from) {
         this.judgeWidth();
        }
    },
	methods:{
		judgeWidth(){
			if (this.$route.path == '/') {
				this.ifheader = false
			} else {
				this.ifheader = true
			}
      this.getNavParam();
		},
    //得到navBar的参数
    getNavParam(){
      var path = this.$route.path;
      let query = this.$route.query;
      var param = this.$route.params;
      if (path == '/News/news' || path == '/News/Renews' ||
       (query.name == 'news' && path == '/Show') 
       || (query.name == 'Renews' && path == '/Publish')
       || (query.name == 'news' && path == '/Publish')) {
          this.showpart = 'homeTab';
          this.showIndex = 0;
          return ;
      }
      else  if(path == '/course' || path == '/CourseAdd' || path == '/CourseDetail') {
          this.showpart = 'homeTab';
          this.showIndex = 1;
          return ;
      }
      else if (path =='/StuManage'){
          this.showpart = 'homeTab';
          this.showIndex = 2;
          return ;
      }
      else if (path =='/ClassChat' || (param.name != 'msg') && path.indexOf('/Communicate/') == 0 
        || (query.name != 'msg') && path.indexOf('/Comment') == 0 ){
          this.showpart = 'homeTab';
          this.showIndex = 3;
          return ;
      }
      else if (path == '/Mymsg') {
          this.showpart = 'homeTab';
          this.showIndex = 4;
          return ;
      }
      else if(path == '/SelfInfo') {
        this.showpart = 'homeTab';
        this.showIndex = 5;
        return ;
      }
      else if (path == '/CourseUpload' || path == '/CourseWare') {
        this.showpart = 'Tab';
        this.showIndex = 1;
        return ;
      } 
      else if (path =='/Communicate/msg' || (query.name == 'msg') && path.indexOf('/Comment') == 0) {
        this.showpart = 'Tab';
        this.showIndex = 2;
        return ;
      }
      else if (path.indexOf('/StuProgress') == 0) {
        this.showpart = 'Tab';
        this.showIndex = 3;
        return ;
      }
      else if (path.indexOf('/Exam/') == 0 || path == '/Homework' 
        || (query.name == 'test' && path == '/Publish') 
        || (query.name == 'test' && path == '/Show')) {
        this.showpart = 'Tab';
        this.showIndex = 4;
        return ;
      } 
       else {
        this.showpart = 'Tab';
        this.showIndex = 0;
      }
    },
    //获取token验证,token 不能有" "
    getToken(){
      var theToken = sessionStorage.getItem('token');
      return theToken.slice(1,theToken.length-1);
    },
    saveState () {
      sessionStorage.setItem('state', JSON.stringify(this.$store.state))
    }
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #F7F7F7;
  width: 100vw;
  min-width: 700px;
  .content{
  	width: 98%;
    margin: 0 auto;
  	min-height: calc(100vh - 70*@allwidth);
  	padding-top: 10*@allwidth;
  	display: flex;
    #main{
      min-height: calc(100% - 60*@allwidth);
      width: calc(100% - 200*@allwidth);
      margin-left: 12*@allwidth;
      margin-bottom: 10*@allwidth;
      border-radius: 10px;
      border: 1px solid #D2D2D2;
      background: #fff;
    }
  }
}
</style>
