<template>
	<div class="showInform">
		<div class="showHead">
			<i class="el-icon-back" @click="goBack()"></i>
			<p>{{title}}详情页</p>
			<div class="annouce" v-show="ifUP">
				<div @click="goCorrect()" class="selectStu">修改</div>
			</div>
		</div>
		<hr>
		<div class="content">
			<div class="newsTitle">{{showData.newsTitle}}</div>
			<div class="author">
				<p v-if="name == 'news'">发布人：{{showData.publisher}}</p>
				<p>发布时间：{{DateTranse(showData.publishTime)}}</p>
			</div>
			<div class="newscontent" v-html="showData.newsContent"></div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'showInform',
	mounted(){
		if (this.getToken()) {
			let query = this.$route.query
			console.log(query.name,query.id)
			this.getContent(query.name,query.id)
		}
	},
	methods:{
		//添加展示样式
		addStyle(str1){
			var str2 = '';
			// console.log(str1);
			str2 = str1.replace(/<img/g,'<img style="width: 100%;height:100%; margin: 1.4987013vw 0;"');
			str2 = str2.replace(/<p class/g,'<p style="text-indent: 2em;margin: 0;font-size: 1.3vw;line-height: 2vw;" class');
      		str2 = str2.replace(/<p>/g,'<p style="text-indent: 2em;margin: 0;font-size: 1.3vw;line-height: 2vw;">');
			this.showData.newsContent = str2;
		},
		//获取token验证,token 不能有" "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		theToken = theToken.slice(1,theToken.length-1);
    		// console.log(theToken);
    		return theToken;
    	},
    	//转换日期格式
		DateTranse(data){
			var d = new Date(data);
			var hour = d.getHours() < 10 ? '0'+ d.getHours():d.getHours();
			var minu = d.getMinutes() < 10 ? '0'+ d.getMinutes():d.getMinutes();
			var sec = d.getSeconds() < 10 ? '0'+ d.getSeconds():d.getSeconds();
			var datetime=d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + hour + ':' + minu + ':' + sec;
			return datetime;
		},
		//获取展示页面类型和ID 
		getContent(name,id){
			var that = this;
			switch(name){
				case 'news':
					this.title = '新闻';
					this.ifUP = true;
					this.getUrl = this.$baseUrl + '/article/'+id;
					break;
				case 'inform':
					this.title = '通知';
					this.getUrl = this.$baseUrl + '/notice/'+ id;
					break;
				case 'test':
					this.ifUP = true;
					this.title = '考试';
					this.getUrl = this.$baseUrl + '/homework/'+ id;
					break;
				case 'homework':
					this.ifUP = true;
					this.title = '作业';
					this.getUrl = this.$baseUrl + '/homework/'+ id;
					break;
				default :
					break;
			}
			this.name = name;
			this.param.set('id',id);
			this.param.set('token',this.getToken());
			this.getDetail(this.getUrl);
		},
		//获取详细信息
		getDetail(url){
			this.axios.post(url,this.param)
			.then((res)=>{
				console.log(res.data)
				if (res.data.code == 200) {
					this.showData.newsTitle = res.data.data.title;
					this.showData.publisher = res.data.data.publisherName;
					this.showData.publishTime = res.data.data.time;
					this.showData.ID = res.data.data.id;
					this.addStyle(res.data.data.content);
					this.imgUrlFun(res.data.data.content);
					this.filterHTMLTag(res.data.data.content)
					// this.showData.newsContent = res.data.data.content;
				} else {
					this.$message.error('获取内容失败！');
				}
			})
		},
		//返回上一页
		goBack() {
	       console.log('go back');
	       this.$router.go(-1)
	    },
	    //进行修改
	    goCorrect(){
	    	this.$router.push({
				path: `/Publish`,
				query:{
					name: 'Re' + this.name,
					id: this.showData.ID,
					// theParam: this.param
				}
			})
	    },
	    //获取富文本里的第一张图片
	    imgUrlFun(str){
	        var data = '';
            str.replace(/<img [^>]*src=['"]([^'"]+)[^>]*>/, function (match, capture) {
                  data =  capture;
            });
            var result = data ? data: 'no'
            console.log(result);
	        // return data;
	    },
	    //获取内容简介(前30个字)
	    filterHTMLTag(str) {
	        str = str.replace(/<\/?[^>]*>/g,''); //去除HTML tag
	        str = str.replace(/[ | ]*\n/g,'\n'); //去除行尾空白
	        str = str.replace(/ /ig,'');//去掉 
	        console.log(str)
	        var endlen = str.length >= 30 ? 30 : str.length;
	        var desc = str.slice(0,endlen);
	        console.log(desc,desc.length)
	        // return str;
		}
	},
	data(){
		return {
      		param: new FormData(),
			title: '',
			getUrl:'',
			name: '',
			ifUP: false,
      		showData:{
      			ID: '',
      			newsTitle: '',
      			newsContent: '',
      			publisher: '',
      			publishTime: ''
      		}
		}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.showInform{
	width: 95%;
	margin: 0 auto;
	hr{
		border: 0.8px solid rgb(61, 126, 141);
		padding: 0;
		margin: 0;
		margin-bottom: 10*@allwidth;
	}
	.showHead{
		height: 65*@allwidth;	
		font-size: 22*@allwidth;
		display: flex;
		align-items: center;
		i{
			font-weight: bold;
		}
		p{
			padding-left: 40*@allwidth;
		}
		.annouce{
			margin-left: auto;
			display: flex;
			.selectStu{
				text-align: center;
				height: 45*@allwidth;
				line-height: 45*@allwidth;
				width: 100*@allwidth;
				color: #fff;
				background: #54CAAB;
				cursor: pointer;
				margin: 0 3*@allwidth;
				border-radius: 5px;
				font-size: 20*@allwidth;
			}
		}
	}
	.content{
		width: 85%;
		margin: 0 auto;
		height: calc(100vh - 170*@allwidth);
		overflow-x: hidden;
		overflow-y: auto;
		span{
			font-size: 18*@allwidth;
			text-indent: 2em;
			line-height: 20*@allwidth;
			font-size: 1.2987013vw;
		    text-indent: 2em;
		    line-height: 1.44300144vw;
		}
		.newsTitle{
			font-size: 25*@allwidth;
			line-height: 50*@allwidth;
			font-weight: bold;
			text-align: center;
		}
		.author{
			margin-left: 59*@allwidth;
			display: flex;
			justify-content: flex-end;
			p{
				margin: 0 20*@allwidth;
				font-size: 16*@allwidth;
				opacity: 0.7;
			}
		}
	}
	.content::-webkit-scrollbar{
		display: none;
	}
}
</style>