import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

var state1 = sessionStorage.getItem('state') ? JSON.parse(sessionStorage.getItem('state')) : {
	cover: 'default.jpg',
	name: '未设置',
  random: Math.random
}

const store = new Vuex.Store({
  state: state1,
  mutations: {
  	name(state,obj){
      state.name = obj
    },
    cover(state,obj){
      state.cover = obj
    },
    random(state,obj){
      state.random = obj
    },
  },
  actions: {

  }
})

export default store;