<template>
	<div class="homework">
		<div class="workhead">
			<i class="el-icon-back" @click="goBack()"></i>
			<p>作业情况</p>
		</div>
		<hr>
		<div :style="{display: noData? 'none':'block'}">
			<div class="StuList">
		  		<el-row v-for="(list,index) in StuList" :key="index">
				  <el-col :span="16">
				  	<div class="main">
				  		<p>{{list.username}}</p>
				  		<p>{{list.name}}</p>
				  	</div>
				  </el-col>
				  <el-col :span="6">
				  	<div class="operate" v-if="list.status">
				  		<a :href="imgUrl+list.url" :download="list.name" style="color: black;"><i class="el-icon-download"></i></a>
				  		<i class="el-icon-success" title="已提交" :style="{color: '#67c23a'}"></i>
				  		<p :style="{'font-size': list.score?'18px':'15px','margin-right':' 5px','color': list.score?'#099c4a':''}">{{list.score ? list.score: '待审阅' }}</p>
				  	</div>
				  	<div class="operate" v-else>
				  		<p style="padding-left: -.7vw;">未提交</p>
				  		<i class="el-icon-remove" title="未提交"></i>
				  	</div>
				  </el-col>
				  <el-col :span="2">
					  <div @click="giveScore(index)" class="graded">打分</div>
				  </el-col>
				</el-row>
		  	</div>
		  	<div class="paging">
		  		<el-pagination
			      @current-change="handleCurrentChange"
			      :current-page.sync="activePage"
			      :page-size="5"
			      v-if="ispage"
			      :pager-count="5"
			      layout="prev, pager, next"
			      :total="totalPage">
			    </el-pagination>
		  	</div>
		</div>
		<div :style="{display: noData? 'block':'none'}">
	  		<div class="notice">
		  		<img src="../assets/img/nodata.png">
		  		<p>暂无内容！</p>
		  	</div>
	  	</div>
	  	<el-dialog
		  title="作业评分"
		  :visible.sync="dialogVisible"
		  width="35%">
		  <div>分数：{{stu.score}}</div>
		  <el-slider v-model="stu.score"></el-slider>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="giveCancel()">取 消</el-button>
		    <el-button type="primary" @click="giveConfirm()">确 定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
export default {
	name: 'homework',
	data(){
		return {
			param: new FormData(),
			activePage: 1,
			ispage: false,
			noData: true,
			totalPage: 1,
			category: '',
			workId: '',
			url: '',
			imgUrl: '',
			StuList:[],
			dialogVisible: false,
			rankId: '',
			visible: false,
			num: 1,
			stu: {
				id: '',
				score: 80
			}
		}
	},
	mounted(){
		if (this.getToken()) {
			this.imgUrl = this.$ImgUrl + '/submitted/';
			this.getListParam();
		}
	},
	methods:{
		getListParam(){
			var courseID = sessionStorage.getItem('courseID');
			let query = this.$route.query;
			this.url = this.$baseUrl + '/homework/showSubmit';
			if (query.name == 'homework') {
				this.category = '作业';
			} else {
				this.category = '考试';
			}
			this.param.set('id',query.id);
			this.param.set('courseId',courseID);
			var nowPage = sessionStorage.getItem('nowPage');
			console.log(nowPage)
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
    		this.param.set('token',this.getToken());
			this.param.set('pageNum',6);
			this.param.set('currentPage',this.activePage);
			this.getListData();
    	},
    	getListData(){
    		var that = this;
    		this.axios.post(this.url,this.param)
    		.then((res)=>{
    			console.log(res.data);
    			res = res.data;
    			var theJSON = {};
		        that.StuList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		name: perData.name,
		        		score: perData.score,
		        		url: perData.url,
		        		username: perData.username,
		        		status: perData.url? 1:0
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	that.totalPage = res.data.totalNumber;
		        	if (Array.isArray(res.data.pageData)) {
		        		for(var key of res.data.pageData){
			        		that.StuList.push(formatData(key));
			        	}
		        		that.noData = false;
		        	} else {
		        		that.$message.warning('暂无数据！');
		        	}
		        	if (res.data.totalNumber > 6) {
		        		that.ispage = true;
		        	} else {
		        		that.ispage = false;
		        	}
		        } else {
		        	that.$message.error('获取'+ that.name +'列表失败！');
		        }
    		})
    	},
		//返回上一页
		goBack(){
	       this.$router.go(-1)
		},
		//分页效果
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			this.param.set('currentPage',this.activePage);
			this.getListData();
			//保存页码
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
		},
		//获取token
		getToken(){
		var theToken = sessionStorage.getItem('token');
			return theToken.slice(1,theToken.length-1);
		},
		giveScore(index){
			var id = this.StuList[index].id;
			if (this.StuList[index].status == 0) {
				this.$message.warning('该学生未提交作业！');
				return ;
			}
			this.dialogVisible = true;
			this.rankId = id;
		},
		giveCancel(){
			this.stu.score = 80;
			this.dialogVisible = false;
		},
		giveConfirm(){
			console.log(this.rankId,this.stu.score);
			var that = this;
			var url = this.$baseUrl + '/homework/judge';
			var param = new FormData();
			param.set('token',this.getToken());
			param.set('id',this.rankId);
			param.set('score',this.stu.score);
    		this.axios.post(url,param)
    		.then((res)=>{
    			console.log(res.data);
    			if (res.data.code == 200) {
    				that.stu.score = 80;
    				that.dialogVisible = false;
    				that.getListData();
    			}
    		})
		}
	}
}
</script>

<style lang="less">
@allwidth: 100/1386vw;
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
p{
	margin: 0;
	padding: 0;
}
.homework{
	width: 95%;
	margin: 0 auto;
	hr{
		border: 0.8px solid rgb(61, 126, 141);
	}
	.workhead{
		height: 45*@allwidth;	
		font-size: 22*@allwidth;
		display: flex;
		align-items: flex-end;
		padding-bottom: 10*@allwidth;
		i{
			font-weight: bold;
		}
		p{
			padding-left: 40*@allwidth;
		}
	}
	.StuList{
		padding-top: 5*@allwidth;
		.el-row{
			height: 55*@allwidth;
			border: 1px solid grey;
			border-radius: 5px;
			font-size: 14*@allwidth;
			padding: 0 20*@allwidth;
			margin-bottom: 4*@allwidth;
			color: #999999;
			display: flex;
			align-items: center;
			cursor: pointer;
			.main{
				display: flex;
				align-items: center;
				margin: 5*@allwidth 0;
				p{
					font-size: 18*@allwidth;
					color: #656565;
				}
				p:nth-child(2){
					opacity: 0.7;
					margin-left: 30*@allwidth;
				}
			}
			.graded{
				float: right;
				// opacity: 1;
				height: 45*@allwidth;
				line-height: 45*@allwidth;
				text-align: center;
				width: 45*@allwidth;
				border-radius: 50%;
				background: grey;
				color: #fff;
				font-size: 16*@allwidth;
			}
			.operate{
				font-size: 25*@allwidth;
				margin-left: 16*@allwidth;
				display: flex;
				flex-direction: row-reverse;
				align-items: center;
				p{
					font-size: 15*@allwidth;
				}
				i{
					padding: 0 10*@allwidth;
				}
			}
		}
		.el-row:hover{
			border: 2px solid #58CD77;
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}
.notice{
	width: 99%;
	margin: 10*@allwidth auto;
	height: calc(100vh - 190*@allwidth);
	min-height: 200px;
	box-shadow: 0 0 0.3px 0.3px #5D8486;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	img{
		width: 70px;
		height: 70px;
	}
	p{
		font-size: 18*@allwidth;
		margin: 5*@allwidth;
		color: #5D8486;
	}
}
</style>