<template>
	<div class="StuAcount">
		<div class="stuHead">
			<ul class="stutab">
				<li title="所有学生账号" @click="allStu()">学生账号</li>
			</ul>
			<div class="search">
				<input v-model="searches" type="text" name="search" placeholder="请输入学号/姓名" autocomplete="off">
				<i @click="Searching()" class="el-icon-search"></i>
			</div>
			<ul class="createAcount">
				<li>
					<el-upload
					  class="upload-demo"
					  :action="$baseUrl+'/batchImport'"
					  auto-upload
					  accept="*.xls"
					  :show-file-list="false"
					  :on-success="handleSuc"
					  :on-error="handleErr"
					  :data="excle"
					  >
					  <div style="color: #fff;">批量导入</div>
					</el-upload>
				</li>
				<li @click="appAcount()">创建学生账号</li>
			</ul>
		</div>
		<div :style="{display: noData? 'none':'block'}">
			<ul class="stuAcnt">
				<li>
					<el-row>
					  <el-col :span="5">学号</el-col>
					  <el-col :span="5">姓名</el-col>
					  <el-col :span="5">性别</el-col>
					  <el-col :span="5">电话</el-col>
					  <el-col :span="4">操作</el-col>
					</el-row>
				</li>
				<li v-for="(item,index) in acntList" :key="index">
					<el-row>
					  <el-col :span="5">{{item.stuNum}}</el-col>
					  <el-col :span="5">{{item.name}}</el-col>
					  <el-col :span="5">{{item.sex}}</el-col>
					  <!-- <el-col :span="5">{{item.phone}}</el-col> -->
					  <el-col :span="5">{{item.phone}}</el-col>

					  <i @click="upAcount(index)" class="el-icon-edit-outline"></i>
			  		  <i @click="DelAcount(item.id)" class="el-icon-delete"></i>
					</el-row>
				</li>
			</ul>
			<div class="paging">
		  		<el-pagination
		  		  v-if='ispage'
			      @current-change="handleCurrentChange"
			      :current-page.sync="activePage"
			      :page-size="10"
			      :pager-count="5"
			      layout="prev, pager, next"
			      :total="totalNum">
			    </el-pagination>
		  	</div>
		</div>
		<div :style="{display: noData? 'block':'none'}">
	  		<div class="notice">
		  		<img src="../assets/img/nodata.png">
		  		<p>暂无内容！</p>
		  	</div>
	  	</div>
	  	<el-dialog :title="dialogTitle" width="40%" :visible.sync="dialogFormVisible">
  			<el-input v-model="form.name" autocomplete="off" :maxlength="10" placeholder="请输入学号" oninput="value=value.replace(/[^\d]/g,'')"></el-input>
  		 	<el-input v-if="isUpPsd" v-model="form.psd" autocomplete="off" placeholder="请输入修改密码" style="margin-top: 10px;"></el-input>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="dialogFormVisible = false">取 消</el-button>
		    <el-button type="primary" @click="simpleAcount()">确 定</el-button>
		  </div>
		</el-dialog>
		<!-- 删除账号 -->
		<el-dialog title="请输入身份密码" width="40%" :visible.sync="ownPsd">
			<el-input v-model="form.ownPsd" autocomplete="off" placeholder="请输入密码"></el-input>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="ownPsd = false">取 消</el-button>
		    <el-button type="primary" @click="DoDel()">确 定</el-button>
		  </div>
		</el-dialog>
	</div>
</template>

<script>
export default {
	name: 'StuAcount',
	data(){
		return {
			isSearchnow: false,
			activePage: 1,
			totalNum: 50,
			ispage: false,
			noData: false,
			acntList:[],
      		param: new FormData(),
      		excle: {
      			token: ''
      		},
      		dialogTitle: '',
			dialogFormVisible: false,
			ownPsd: false,
			isUpPsd: false,
			form:{
				name: '',
				psd: '',
				ownPsd: '',
				id: '',
				index: ''
			},
			searches: '',
			formLabelWidth: '120px'
		}
	},
	mounted(){
		if (this.getToken()) {
			this.addParamValue();
		}
	},
	methods:{
		//分页跳转
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			//更新路由参数的值
			this.param.set('currentPage',this.activePage);
			if (this.isSearchnow) {
				this.Searching();
			} else {
				this.getAcount();
			}
			//保存页码
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
		},
		//设置请求参数
		addParamValue(){
			//根据路由的参数设置activePage的值
			var nowPage = sessionStorage.getItem('nowPage');
			console.log(nowPage)
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.set('token',this.getToken());
			this.excle.token = this.getToken();
			this.param.set('currentPage',this.activePage);
			this.param.set('pageNum',10);
			this.getAcount();
		},
		// 获取账号列表
		getAcount(Param){
			var that = this;
			var gParam = Param? Param: this.param;
			this.axios.post(this.$baseUrl + '/students',gParam)
			.then((res)=>{
				console.log(res.data)
				var theJSON = {};
		        that.acntList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		stuNum: perData.username,
		        		name: perData.name? perData.name:'未设置',
		        		// phone: perData.phone || '空',
		        		phone: perData.phone?perData.phone:'未设置',
		        		sex: perData.sexual? perData.sexual:'未设置'
		        	}
		        	return theJSON;
		        }
				if (res.data.code == 200) {
					res = res.data.data;
					that.totalNum = res.totalNumber;
					if (Array.isArray(res.pageData)) {
		        		for(var key of res.pageData){
			        		that.acntList.push(formatData(key));
			        	}
		        	} else {
		        		that.$message.warning(that.name + '列表为空！');
		        		that.noData = true;
		        	}
		        	if (res.totalNumber > 10) {
		        		that.ispage = true;
		        	}  else {
		        		that.ispage = false;
		        	}
		        } else {
		        	that.$message.error('获取'+ that.name +'列表失败！');
		        }
			})
			.catch((e)=>{
				console.log(e)
			})
		},
		//选定修改账号
		upAcount(index){
			this.dialogFormVisible = true;
			this.dialogTitle = '修改学生账号';
			this.isUpPsd = true;
			this.form.name = this.acntList[index].stuNum;
			this.form.id = this.acntList[index].id;
			this.form.index = index;
		},
		//选定添加账号
		appAcount(){
			this.dialogFormVisible = true;
			this.isUpPsd = false;
			this.dialogTitle = '创建学生账号';
		},
		//增加/修改账号
		simpleAcount(){
			var that = this;
			if (this.form.name.length != 10) {
				this.$message.error('请输入10位学号！');
				return ;
			} 
			var param = new FormData();
			param.set('token',this.getToken());
			if (this.dialogTitle == '创建学生账号') {
				param.set('username',this.form.name);
				this.axios.post(this.$baseUrl+'/student/add',param)
				.then((res)=>{
					console.log(res.data);
					if (res.data.code == 200) {
						that.$message.success('账号创建成功！');
						that.getAcount();
					} else {
						that.$message.error(res.data.message);
					}
					this.dialogFormVisible = false;
					this.form.name = '';
				})
				.catch((e) =>{
					console.log(e);
				})
			} else {
				if (this.form.name == this.acntList[this.form.index].stuNum && this.form.psd == '') {
					this.$message.error('请确定有修改内容！');
					return ;
				}
				if (this.form.psd != '') {
					if (this.form.psd.length < 6) {
						this.$message.error('密码不能少于6位！');
						return ;
					} else {
						param.set('password',this.form.psd);
					}
				}
				param.set('username',this.form.name);
				param.set('id',this.form.id);
				this.axios.post(this.$baseUrl+'/student/update',param)
				.then((res)=>{
					console.log(res.data);
					if (res.data.code == 200) {
						that.$message.success('账号修改成功！');
						that.getAcount();
					} else {
						that.$message.error(res.data.message);
					}
					this.dialogFormVisible = false;
					this.form.name = '';
					this.form.psd = '';
				})
				.catch((e) =>{
					console.log(e);
				})
			}
		},
		//删除账号
		DelAcount(id){
			var that = this;
			this.$confirm('确认删除该条信息？')
			.then(()=>{
				// that.ifDel(id);
				that.ownPsd = true;
				that.form.id = id;
			})
			.catch(()=>{})
		},
		DoDel(){
			var that = this;
			var url = this.$baseUrl + '/student/delete';
			var Dparam = new FormData();
			Dparam.set('id',this.form.id);
			Dparam.set('password',this.form.ownPsd);
			console.log(this.form.ownPsd);
			Dparam.set('token',this.getToken());
			this.axios.post(url,Dparam)
			.then((res)=>{
				console.log(res.data);
				if (res.data.code == 200) {
					that.getAcount();
					that.$message.success('删除成功！');
				} else {
					that.$message.error(res.data.message);
				}
				that.form.ownPsd = '';
				that.ownPsd = false;
			})
			.catch(()=>{})
		},
		//查找
		Searching(){
			var that = this;
			var param = new FormData();
			if (this.searches == '') {
				this.$message.error('请输入查找信息！');
				return ;
			} 
			if (this.searches[0] <= '9' && this.searches[0] >= '0') {
				param.set('stuNum',this.searches);
			} else {
				param.set('stuName',this.searches);
			}
			param.set('token',this.param.get('token'));
			if (this.isSearchnow == false) {
				this.isSearchnow = true;
				this.activePage = 1;
				param.set('currentPage',1);
			} else {
				param.set('currentPage',this.activePage);
			}
			console.log(this.activePage);
			param.set('pageNum',this.param.get('pageNum'));
			console.log(param.get('pageNum'))
			this.getAcount(param);
		},
		//批量导入账号
		handleSuc(res, file, fileList){
			console.log(res);
			if (res.code == 200) {
				this.$message.success(res.message);
			}
		},
		handleErr(err, file, fileList){
			if (fileList.length == 0) {
				console.log('上传失败！')
			}
			console.log(fileList);
		},
		//获取token验证,token 不能有" "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	},
    	allStu(){
    		if (this.isSearchnow == true) {
				this.isSearchnow = false;
				this.activePage = 1;
				this.searches = '';
				this.param.set('currentPage',1);
			} else {
				this.param.set('currentPage',this.activePage);
			}
    		this.getAcount();
    	}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
.StuAcount{
	width: 95%;
	margin: 0 auto;
	.stuHead{
		height: 65*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		li{
			text-align: center;
			height: 40*@allwidth;
			line-height: 40*@allwidth;
			padding: 0 12*@allwidth;
			color: #fff;
			background: #316694;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 19*@allwidth;
		}
		.search{
			border: 1px solid grey;
			border-radius: 5px;
			input{
				width: 250*@allwidth;
				height: 36*@allwidth;
				line-height: 36*@allwidth;
				padding: 0 10*@allwidth;
				font-size: 18*@allwidth;
				border-radius: 5px;
				outline: none;
				border: 0;
			}
			i{
				font-size: 22*@allwidth;
				font-weight: bold;
				padding-right: 10*@allwidth;
				cursor: pointer;
			}
			i:hover{
				color: red;
			}
		}
		.search:hover{
			border-color: #409eff;
		}
		.createAcount{
			line-height: 45*@allwidth;
			display: flex;
			li{
				background: #316694;
			}
			li:first-child{
				background: #226f40;
			}
		}
	}
	.stuAcnt{
		margin: 10*@allwidth 0;
		li .el-row {
			border-bottom: 1px solid #D2D2D2;
			text-align: center;
			.el-col{
				line-height: 33*@allwidth;
				font-size: 18*@allwidth;
			}
			i{
				line-height: 33*@allwidth;
				font-size: 25*@allwidth;
				padding: 0 5*@allwidth;
				cursor: pointer;
			}
			i:hover{
				color: #9CE76B;
			}
			i:last-child:hover{
				color: red;
			}
		}
		li:first-child{
			.el-row{
				background: rgba(144, 147, 153,0.3);
				.el-col{
					line-height: 40*@allwidth;
				}
			}
		}
		li:hover{
			background: rgba(144, 147, 153,0.1);
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}
.notice{
	width: 99%;
	margin: 10*@allwidth auto;
	height: calc(100vh - 190*@allwidth);
	min-height: 200px;
	box-shadow: 0 0 0.3px 0.3px #5D8486;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	img{
		width: 70px;
		height: 70px;
	}
	p{
		font-size: 18*@allwidth;
		margin: 5*@allwidth;
		color: #5D8486;
	}
}
</style>