<template>
	<div class="communityPart">
		<div class="workhead">
			<i class="el-icon-back" @click="goBack()"></i>
			<p class="title">{{LTie.Ltitle}}</p>
		</div>
		<hr>
		<ul class="TieList">
			<li class="Lzhu">
				<img :src="imgUrl + LTie.cover + '?number=' + $store.state.random">
				<div class="part">
					<p class="headtab">
						{{LTie.name}}
						<span v-if="LTie.Lrank == 1? true:false">老师</span>
						<span>{{LTie.Ltime}}</span>
					</p>
					<p v-html="LTie.Lcontent"></p>
					<p @click="replyBtn(-1)" class="reply">回复</p>
				</div>
			</li>
			<li v-for="(item,index) in TieData" :key="index">
				<img :src="imgUrl + item.cover + '?number=' + $store.state.random">
				<div class="part">
					<p class="headtab">{{item.name}}
						<span v-if="item.Arank == 1? true:false">老师</span>
						<span>{{item.Atime}}</span>
					</p>
					<p>{{item.Acontent}}</p>
					<p class="reply">
						<span @click="replyBtn(item.Aid)">回复</span>
					</p>
					<div class="TtwiceCmt" v-for="(tcmt,tindex) in CommentData" :key="tindex">
						<ul v-if="item.Aid == tcmt.elderID">
							<li v-for="(tcmts,tsindex) in tcmt.cmList" :key="tsindex">
								<img :src="imgUrl + tcmts.cover + '?number=' + $store.state.random">
								<div class="tpart">
									<p class="headtab">
										{{tcmts.name}}
										<span v-if="tcmts.Arank == 1? true:false" class="tab">老师</span>
										<span>{{tcmts.Atime}}</span>
									</p>
									<p>{{tcmts.Acontent}}{{tcmts.whenAnswer}}</p>
									<p @click="replyBtn(tcmts.Aid)" class="reply">回复</p>
								</div>
							</li>
						</ul>
					</div>
				</div>
			</li>
		</ul>
		<div v-if="isreply" class="Input">
			<input v-model="Treply" type="text" name="reply" placeholder="输入你的回复" autocomplete="off">
			<p @click="Comment()" class="sendBtn">回复</p>
		</div>	
	</div>
</template>

<script>
export default {
	name: 'communityPart',
	data(){
		return {
			imgUrl: '',
			param: new FormData(),
			replyParam: new FormData(),
			name: '',
			TieId: '',
			TieUrl: '',
			Treply: '',
			isreply: false,
			content: '',
			LTie:{
				TieID: '',
				courseID: '',
				name: '',
				cover: 'default.jpg',
				Ltitle: '',
				Lcontent: '',
				Lrank: '',
				Ltime: '',
				answering: false
			},
			TieData:[],
			CommentData: [],
		}
	},
	mounted(){
		if (this.getToken()) {
			this.getTitleInfo();
			this.imgUrl = this.$ImgUrl + '/avatar/';
		}
	},
	methods:{
		//添加展示样式
		addStyle(str1){
			var str2 = '';
			// console.log(str1);
			str2 = str1.replace(/<img/g,'<img style="width: 300px;margin: 1vw 0;"');
			str2 = str2.replace(/<p class/g,'<p style="text-indent: 2em;margin: 0;font-size: 1.3vw;line-height: 1.8vw;" class');
      		str2 = str2.replace(/<p>/g,'<p style="text-indent: 2em;margin: 0;font-size: 1.3vw;line-height: 1.8vw;">');
			return str2;
		},
		//获取标题信息
		getTitleInfo(){
			var query = this.$route.query;
			this.TieId = query.id;
			this.name = query.name;
			if (query.name == 'msg') {
				this.TieUrl = this.$baseUrl + '/courseDiscussion/question/' + this.TieId;
			} else {
				this.TieUrl = this.$baseUrl + '/ClassDiscussion/question/' + this.TieId;
			}
			
			this.param.set('token',this.getToken());
			this.viewTie();
		},
		//查看具体帖子
		viewTie(){
			var that = this;
			this.axios.post(this.TieUrl,this.param)
	      	.then((response)=>{
	      		var res = response.data;
	      		// console.log(res);
		        var theJSON = {};
		        that.TieData = [];
		        that.CommentData = [];
		        //规范楼主格式
		        var LTieFormat = function(Ldata){
		        	theJSON = {
		        		TieID : Ldata.id,
						courseID: Ldata.courseId,
						name: Ldata.userDto.name,
						cover: Ldata.userDto.avatar,
						Ltitle: Ldata.title,
						Lcontent: Ldata.content,
						Lrank: Ldata.isTeacher,
						Ltime: Ldata.time,
						answering: false
		        	}
		        	return theJSON;
		        };
		        //规范回帖格式
		        var formatData = function(perData){
		        	theJSON = {
		        		Aid: perData.id,
		        		AsuperID: perData.superiorId,
		        		Acontent: perData.content,
		        		name: perData.respondent.name,
		        		cover: perData.respondent.avatar,
		        		Atime: perData.time,
		        		Arank: perData.isTeacher,
		        		whenAnswer: ''
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	console.log(res.data);
		        	that.LTie = LTieFormat(res.data);
		        	that.LTie.Lcontent = that.addStyle(that.LTie.Lcontent);
		        	// console.log(that.LTie.Lcontent);
		        	if (Array.isArray(res.data.answerList)) {
		        		for(var key of res.data.answerList){
		        			if (key.superiorId == -1) {
		        				//获取第一层回复
		        				that.TieData.push(formatData(key));
		        			} else {
		        				//获取评论
		        				var cmData = {
		        					elderID: key.superiorId,
		        					cmList: [],
		        					cmIds: []
		        				};
		        				var atData = '';
		        				var index = -1;
		        				var orginID = '';
		        				if (that.CommentData.length != 0) {
		        					//判断是否是回复的回复
		        					for(let i = 0; i < that.CommentData.length; i++) {
		        						var cmJ = that.CommentData[i];
		        						for(let j = 0; j < cmJ.cmIds.length; j++){
		        							//如果帖子的上级id是二级回复，就记录一级回复id，和二级回复id
		        							if (key.superiorId == cmJ.cmIds[j] ) {
		        								orginID = that.CommentData[i].elderID;
		        								atData = ' //@ ' + cmJ.cmList[j].name + ' : ' + cmJ.cmList[j].Acontent;
		        								// console.log(orginID,atData);
		        							}
		        						}
		        					}
		        					// console.log(orginID);
		        					//判断是否是已经报保存的上级ID
		        					for(let i = 0; i < that.CommentData.length; i++) {
		        						orginID = orginID == ''? key.superiorId: orginID;
		        						if (orginID == that.CommentData[i].elderID) {
		        							that.CommentData[i].cmIds.push(key.id);
		        							index = i;
		        						}
		        					}
		        				}
		        				// console.log(index);
		        				if (index == -1) {
			        				cmData.cmList.push(formatData(key));
			        				cmData.cmIds.push(key.id);
			        				that.CommentData.push(cmData);
		        				} else {
		        					if (atData != '') {
		        						var replyAgain = formatData(key);
		        						replyAgain.whenAnswer = atData;
		        						that.CommentData[index].cmList.push(replyAgain);
		        					} else {
		        						that.CommentData[index].cmList.push(formatData(key));
		        					}
		        				}
		        			}
			        	}
			        	// console.log(that.CommentData);
		        	} else {
		        		that.$message.warning(that.name + '列表为空！');
		        	}
		        } else {
		        	that.$message.error('获取'+ that.name +'列表失败！');
		        }
		     })
	      	.catch((e)=>{
	      		console.log(e)
		     })
		},
		//返回上一页
		goBack(){
			this.$router.go(-1);
		},
		replyBtn(pubid){
			this.isreply = true;
			// //清空原始数据
			// this.param.delete('superiorId');
			// this.param.delete('qsrId');
			// this.param.delete('content');
			//添加数据
			this.param.set('superiorId',pubid);
			this.param.set('qsId',this.TieId);
			// this.param.set('token',this.getToken());
		},
		//获取评论信息
		showInfo(e){
			this.content = e;
		},
		//控制显示回复的回复
		ifshowAnswer(index){
			this.TieData[index].answering = !this.TieData[index].answering;
			console.log(this.TieData[index].answering,index)
		},
		Comment(){
			var that = this;
			var url = '';
			if (this.name == 'msg') {
				url = this.$baseUrl + '/courseDiscussion/reply';
				var courseId = sessionStorage.getItem('courseID');
				this.param.set('courseId',courseId);
			} else {
				url = this.$baseUrl + '/ClassDiscussion/reply';
			}
			this.param.set('content',this.Treply);
			this.axios.post(url,this.param)
			.then((res)=>{
				console.log(res.data);
				if(res.data.code == 200){
					that.Treply = '';
					that.isreply = false;
					that.viewTie();
				}
			})
			.catch((e)=>{
				console.log(e);
			})
		},
		//获取token验证,token 不能有" "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		if (theToken == null) {
    			return;
    		}
    		return theToken.slice(1,theToken.length-1);
    	}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
p{
	margin: 0;
	padding: 0;
}
.communityPart{
	width: 95%;
	margin: 0 auto;
	hr{
		border: 0.8px solid rgb(61, 126, 141);
		margin: 10*@allwidth 0;
	}
	.workhead{
		height: 40*@allwidth;
		font-size: 22*@allwidth;
		display: flex;
		align-items: flex-end;
		i{
			font-weight: bold;
		}
		p{
			padding-left: 40*@allwidth;
		}
	}
	.TieList{
		width: 100%;
		position: relative;
		height: calc(100vh - 160*@allwidth);
		min-height: 210px;
		overflow-x: hidden;
		overflow-y: auto;
		li{
			display: flex;
			align-items: flex-start;
			padding: 6*@allwidth 0;
			border-bottom: 1px solid #D2D2D2;
			img{
				width: 50*@allwidth;
				height: 50*@allwidth;
				border-radius: 50%;
				margin: 0 15*@allwidth;
			}
			.part{
				width: calc(100% - 40*@allwidth);
				font-size: 16*@allwidth;
				padding: 0 20*@allwidth;
				.title{
					text-align: center;
					font-size: 20*@allwidth;
				}
				.headtab{
					span{
						opacity: 0.8;
						margin-left: 5*@allwidth;
					}
					.tab{
						border: 1px solid green;
						font-size: 12*@allwidth;
					}
				}
				p{
					padding: 6*@allwidth 0;
				}
				p:first-child{
					color: #406599;
				}
				p:last-child{
					color: #406599;
					cursor: pointer;
					float: right;
				}
				.TtwiceCmt{
					width: 100%;
					ul {
						li{
							border: 0;
							border-top: 0.5px solid grey;
							.tpart{
								width: calc(100% - 40*@allwidth);
								p{
									padding: 4*@allwidth 0;
								}
							}
						}
					} 
				}
				.reply{
					color: #406599;
					display: flex;
					justify-content: flex-end;
					span{
						cursor: pointer;
						margin-right: 5*@allwidth;
					}
				}
			}
		}
		.Lzhu{
			border: 1.5px solid #2DCB58;
			border-radius: 15*@allwidth;
			img{
				width: 60*@allwidth;
				height: 60*@allwidth;
			}
		}
		
	}
	.Input{
		width: calc(100% - 300*@allwidth);
		position: absolute;
		bottom: 11*@allwidth;
		padding: 2*@allwidth 0;
		background: #fff;
		display: flex;
		line-height: 40*@allwidth;
		border: 0;
		input{
			outline: none;
			width: calc(100% - 80*@allwidth);
			padding: 10*@allwidth;
		}
		.sendBtn{
			width: 80*@allwidth;
			text-align: center;
			background: black;
			color: #fff;
			font-size: 16*@allwidth;
			cursor: pointer;
		}
	}
}
</style>