<template>
  <div class="navBar" >
    <div class="userInfo">
    	<img :src="imgUrl+ $store.state.cover + '?number=' + $store.state.random">
    	<div class="username">
    		{{this.$store.state.name}}
    	</div>
    </div>
    <ul class="Bar">
    	<li @click="getIndex(index)" :class="[showIndex == index ? 'isActive':'']" v-for="(item,index) in showTab" :key="index">
    		<i :class="item.icon"></i>
    		<p>{{item.name}}</p>
    	</li>
    </ul>
    <div v-show="showBack" class="comeBack" @click="goCouse">返回课程列表</div>
  </div>
</template>

<script>
export default{
	name: 'navBar',
	props:['showPart','showIndex'],
	data(){
		return {
			selfmenu: 'menu',
			showBack: false,
			navBarFixed: false,
			nCourse: '',
			imgUrl: '',
			showTab:[],
			homeTab:[{
				path: '/News/news',
				icon: 'el-icon-news',
				name: '新闻管理'
			},{
				path: '/course',
				icon: 'el-icon-reading',
				name: '课程管理'
			},{
				path: '/StuManage',
				icon: 'el-icon-place',
				name: '学生管理'
			},{
				path: '/ClassChat',
				icon: 'el-icon-bell',
				name: '班级管理'
			},{
				path: '/Mymsg',
				icon: 'el-icon-chat-line-square',
				name: '我的消息'
			},{
				path: '/SelfInfo',
				icon: 'el-icon-medal',
				name: '个人中心'
			}],
			Tab:[{
				// id: '1',
				path: '/News/inform',
				icon: 'el-icon-discover',
				name: '通知发布'
			},{
				// id: '2',
				path: '/CourseUpload',
				icon: 'el-icon-s-platform',
				name: '课程管理'
			},{
				// id: '3',
				path: '/Communicate/msg',
				icon: 'el-icon-chat-line-round',
				name: '交流模块'
			},{
				// id: '4',
				path: '/StuProgress',
				icon: 'el-icon-user-solid',
				name: '学生进度'
			},{
				// id: '5',
				path: '/Exam/homework',
				icon: 'el-icon-edit',
				name: '考试管理'
			}]
		}
	},
	mounted(){
		this.securityConfirm();
		console.log(this.$store.state.cover)
	},
	methods: {
		//element-ui 控制活动的导航
        handleOpen(key, keyPath) {
        	console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
        	console.log(key, keyPath);
        },
        getIndex(index){
        	this.ifactive = index;
        	if (this.showPart == 'homeTab') {
        		this.$router.push(this.homeTab[index].path);
        	} else {
        		this.$router.push(this.Tab[index].path);
        	}
        	sessionStorage.setItem('nowPage', JSON.stringify(1));
        },
		//确定登录后访问
		securityConfirm(){
			// var sData = sessionStorage.getItem('userInfo');
			var sData = sessionStorage.getItem('token');
			// console.log(sData)
	        if(sData==null){
	          this.$message.warning('请先登录!');
	          this.$router.push('/');
	          return;
	        }
	        this.imgUrl = this.$ImgUrl + '/avatar/';
	        this.getTab();
		},
        getTab(){
	  		// console.log(val)
	  		var val = this.showPart;
	  		if (val == 'homeTab') {
	  			this.showTab = this.homeTab;
	  			this.showBack = false;
	  		}
	  		if (val == 'Tab') {
	  			this.showTab = this.Tab;
        		// var courseName = sessionStorage.getItem('courseName');
        		// if (courseName) {
        		// 	this.nCourse = courseName.slice(1,courseName.length-1);
        		// }
	  			this.showBack = true;
	  		}
		},
		goCouse(){
			this.$router.push('/course');
		},
    }
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.navBar{
	width: 200*@allwidth;
	.userInfo{
		width: 100%;
		height: 108*@allwidth;
		display: flex;
		align-items: center;
		justify-content: center;
		border: 1px solid #D2D2D2;
		border-radius: 10px;
		background: #fff;
		img{
			width: 50*@allwidth;
			height: 50*@allwidth;
			border-radius: 50%;
		}
		.username{
			width: 40%;
			text-align: center;
			font-size: 18*@allwidth;
		}
	}
	.tab{
		margin-top: 15*@allwidth;
		border: 1px solid #D2D2D2;
		border-radius: 10px;
	}
	.Bar{
		margin-top: 15*@allwidth;
		background: #fff;
		padding: 0;
		border-radius: 5px;
		border: 1px solid #D2D2D2;
		li{
			height: 3.8961039vw;
			line-height: 3.8961039vw;
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 20*@allwidth;
			border-radius: 3px;
			cursor: pointer;
			p{
				margin: 0 5*@allwidth;
			}
		}
		.isActive{
			color: #409EFF;
			background: #ecf5ff;
		}
		li:hover{
			color: #409EFF;
		}
	}
	.comeBack{
		margin: 30*@allwidth 0;
		height: 3.8961039vw;
		line-height: 3.8961039vw;
		font-size: 19*@allwidth;
		text-align: center;
		background: #fff;
		color: #409EFF;
		cursor: pointer;
		border-radius: 10px;
		border: 1px solid #D2D2D2;
	}
}
</style>