<template>
	<div class="course">
		<ul class="newsHead">
			<li>班级列表</li>
  			<li @click="addClass()">添加班级</li>
  		</ul>
  		<div :style="{display: noData? 'none':'block'}">
  			<ul class="showCourse">
	  			<li v-for="(item,index) in DataList" :key="index">
	  				<div class="name" @click="goCommunity(item.id)">{{item.name}}</div>
	  				<div class="desc">
	  					<p>{{item.desc}}</p>
	  					<div class="doubI">
	  						<i @click="UpClass(index)" class="el-icon-edit-outline"></i>
							<i @click="ifDelClass(item.id)" class="el-icon-delete"></i>
	  					</div>
	  				</div>
	  			</li>
	  		</ul>
	  		<div class="paging">
		  		<el-pagination
		  		  v-if='ispage'
			      @current-change="handleCurrentChange"
			      :current-page.sync="activePage"
			      :page-size="12"
			      :pager-count="5"
			      layout="prev, pager, next"
			      :total="totalPage">
			    </el-pagination>
		  	</div>
  		</div>
  		<div :style="{display: noData? 'block':'none'}">
	  		<div class="notice">
		  		<img src="../assets/img/nodata.png">
		  		<p>暂无内容！</p>
		  	</div>
	  	</div>
  		<el-dialog
  			title="添加班级"
		  	:visible.sync="dialogVisible"
		  	width="45%">
		  	<el-form :model="pubForm" class="demo-ruleForm" style="width: 85%;margin: 0 auto;">
				<el-form-item label="班级" prop="name" style="margin-bottom: 0;">
				    <el-input v-model="pubForm.title" :maxlength="10" placeholder="输入班级号"  oninput = "value=value.replace(/[^\d]/g,'')"></el-input>
				</el-form-item>
				<el-form-item label="描述" prop="desc">
				    <el-input type="textarea" :rows="3" :maxlength="50" v-model="pubForm.content" placeholder="输入50字内描述"></el-input>
				</el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="canselSpeach()">取消</el-button>
		    <el-button type="primary" @click="pubSpeach()">确定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
export default {
	name: 'course',
	data(){
		return {
			param: new FormData(),
			dialogVisible: false,
			ispage: false,
			activePage: 1,
			totalPage: 1,
			noData: true,
			classUrl: '',
			pubForm:{
				id: '',
				title: '',
				content: ''
			},
			DataList:[],
			testList:[{
				id: '12',
				name: '给他与他',
				desc: '共和国附件'
			}]
		}
	},
    mounted(){
    	if (this.getToken()) {
    		this.getListParam();
    	}
    },
	methods:{
		//为获取班级列表添加参数
		getListParam(){
			var nowPage = sessionStorage.getItem('nowPage');
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.set('token',this.getToken());
			this.param.set('currentPage',this.activePage);
			this.param.set('pageNum',12);
			this.classUrl = this.$baseUrl + '/class/show';
			this.realGotData();
		},
		//获取班级列表
		realGotData(){
			var that = this;
			this.axios.post(this.classUrl,this.param)
			.then((response)=>{
	      		var res = response.data;
		        var theJSON = {};
		        that.DataList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		name: perData.classNum,
		        		desc: perData.description
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	console.log(res.data)
		        	that.totalPage = res.data.totalNumber;
		        	if (Array.isArray(res.data.pageData)) {
		        		for(var key of res.data.pageData){
			        		that.DataList.push(formatData(key));
			        	}
		        		that.noData = false;
		        	} else {
		        		that.$message.warning(that.name + '列表为空！');
		        	}
		        	//大于一页才显示分页
		        	if (that.totalPage > 12) {
		        		that.ispage = true;
		        	} else {
		        		that.ispage = false;
		        	}
		        } else {
		        	that.$message.error('获取'+ that.name +'列表失败！');
		        }
		     })
			.catch((e)=>{
				console.log(e);
				that.DataList = that.testList;
			})
		},
		//创建课程添加参数
		addClass(){
			this.dialogVisible = true;
		},
		//取消创建
		canselSpeach(){
			this.pubForm.id = '';
			this.pubForm.title = '';
			this.pubForm.content = '';
			this.dialogVisible = false;
		},
		//创建班级
		pubSpeach(){
			if (this.pubForm.title == '') {
				this.$message.error('创建班级不能为空！');
				return ;
			}
			if (this.pubForm.content == '') {
				this.$message.error('班级描述不能为空！');
				return ;
			}
			var that = this;
			var pubSpeaParam = new FormData();
			var msg = '';
			var url = '';
			var id = this.pubForm.id;
			if (id) {
				url = this.$baseUrl + '/class/update';
				pubSpeaParam.set('id',id);
				msg = '修改班级';

			} else {
				url = this.$baseUrl + '/class/create';
				msg = '创建班级';
			}
			pubSpeaParam.set('token',this.param.get('token'));
			pubSpeaParam.set('classNum',this.pubForm.title);
			pubSpeaParam.set('description',this.pubForm.content);
			this.axios.post(url,pubSpeaParam)
			.then((res)=>{
				console.log(res.data);
				if (res.data.code == 200) {
					that.$message.success(msg + '成功！');
					//清空输入
					that.canselSpeach();
					that.realGotData();
				} else {
					that.$message.error(msg +'失败！');
				}
			})
			.catch((e)=>{
				console.log(e);
			})
		},
		//修改班级
		UpClass(index){
			this.dialogVisible = true;
			this.pubForm.id = this.DataList[index].id;
			this.pubForm.title = this.DataList[index].name;
			this.pubForm.content = this.DataList[index].desc;
		},
		//删除班级
		ifDelClass(id){
			var that = this;
			this.$confirm('确认删除该条信息？')
			.then(()=>{
				that.DoDel(id);
			})
			.catch(()=>{})
		},
		DoDel(id){
			var that = this;
			var url = this.$baseUrl + '/class/delete';
			var param = new FormData();
			param.set('token',this.getToken());
			param.set('id',id);
			this.axios.post(url,param)
			.then((res)=>{
				console.log(res);
				if (res.data.code == 200) {
					that.realGotData();
					that.$message.success('删除成功！');
				}  else {
					that.$message.error('删除失败！');
				}
			})
			.catch((e)=>{
				console.log(e);
			})
		},
		//从班级进入发言列表
		goCommunity(id){
			this.$router.push('/Communicate/' + id);
		},
		//获取token验证,token 不能有" "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		if (theToken == null) {
    			return;
    		}
    		return theToken.slice(1,theToken.length-1);
    	},
    	//分页跳转
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			//更新路由参数的值
			this.param.set('currentPage',this.activePage);
			this.realGotData();
			//保存页码
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
		},
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
.course{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 55*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			height: 45*@allwidth;
			line-height: 45*@allwidth;
			width: 100*@allwidth;
			color: #fff;
			background: #099c4a;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 20*@allwidth;
		}
		li:last-child{
			margin-left: auto;
			background: #28acc1;
		}
	}
	.showCourse{
		width: 100%;
		margin-top: 5*@allwidth;
		display: flex;
		flex-wrap: wrap;
		li{
			width: 18%;
			height: 88*@allwidth;
			border: 2px solid #D2D2D2;
			border-radius: 5px;
			margin: 5*@allwidth 0.6%;
			text-align: center;
			cursor: pointer;
			.name{
				width: 65%;
				margin: 0 auto;
				font-size: 23*@allwidth;
				margin-top: 25*@allwidth;
			}
			.desc{
				width: 80%;
				margin: 0 auto;
				font-size: 13*@allwidth;
				margin-top: 10*@allwidth;
				display: flex;
				align-items: center;
				justify-content: center;
				p{
					margin: 0;
					padding: 0;
					width: calc(100% - 40*@allwidth);
					text-align: center;
					white-space: nowrap;
					text-overflow: ellipsis;
					overflow: hidden;
					word-break: break-all;
				}
				.doubI{
					display: none;
					width: 40*@allwidth;
					i{
						padding: 0 1*@allwidth;
					}
					i:hover{
						color: red;
					}
					i:first-child:hover{
						color: blue;
					}
				}
			}
			.desc:hover{
				.doubI{
					display: block;
				}
			}
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}
//修改element-ui 的样式
.course /deep/ .el-dialog__body {
    padding: 0;
}
.notice{
	width: 99%;
	margin: 10*@allwidth auto;
	height: calc(100vh - 190*@allwidth);
	min-height: 200px;
	box-shadow: 0 0 0.3px 0.3px #5D8486;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	img{
		width: 70px;
		height: 70px;
	}
	p{
		font-size: 18*@allwidth;
		margin: 5*@allwidth;
		color: #5D8486;
	}
}
</style>