<template>
	<div class="StuProgress">
		<div class="stuHead">
			<ul>
				<li @click="allStu()">{{stuTitle}} </li>
			</ul>
			<div class="search">
				<input v-model="searches" type="text" name="search" placeholder="请输入学号/姓名" autocomplete="off">
				<i @click="Searching()" class="el-icon-search"></i>
			</div>
		</div>
		<div :style="{display: noData? 'none':'block'}">
			<ul class="stuTable">
				<li>
					<el-row>
					  <el-col :span="4">学号</el-col>
					  <el-col :span="4">姓名</el-col>
					  <el-col :span="5">手机号</el-col>
					  <el-col :span="4">是否结业</el-col>
					  <el-col :span="7">学习进度</el-col>
					</el-row>
				</li>
				<li v-for="(item,index) in TableList" :key="index">
					<el-row>
					  <el-col :span="4">{{item.acount}}</el-col>
					  <el-col :span="4">{{item.name}}</el-col>
					  <el-col :span="5">{{item.phone}}</el-col>
					  <el-col :span="4">{{item.graduated}}</el-col>
					  <el-col :span="7" style="padding-top: 1px;">
					  	<el-progress :percentage="item.percent"></el-progress>
					  </el-col>
					</el-row>
				</li>
			</ul>
			<div class="paging">
		  		<el-pagination
		  		  v-if='ispage'
			      @current-change="handleCurrentChange"
			      :current-page.sync="activePage"
			      :page-size="12"
			      :pager-count="5"
			      layout="prev, pager, next"
			      :total="totalPage">
			    </el-pagination>
		  	</div>
		</div>
		<div :style="{display: noData? 'block':'none'}">
  		<div class="notice">
	  		<img src="../assets/img/nodata.png">
	  		<p>暂无内容！</p>
	  	</div>
  	</div>
	</div>
</template>

<script>
export default {
	name: 'StuProgress',
	data(){
		return {
			isearch: false,
			param: new FormData(),
			stuTitle: '学生进度管理',
			searching: '',
			activePage: 1,
			totalPage: 1,
			ispage: false,
			courseId: '',
			searches: '',
			TableList:[],
			noData: true
		}
	},
	mounted(){
		if (this.getToken()) {
			this.addParamValue();
		}
	},
	methods:{
		//分页跳转
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			//更新路由参数的值
			this.param.set('currentPage',this.activePage);
			if (this.isearch) {
				this.Searching();
			} else {
				this.getAcount();
			}
			//保存页码
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
		},
		//设置请求参数
		addParamValue(){
			//根据路由的参数设置activePage的值
			this.courseId = sessionStorage.getItem('courseID');
			var nowPage = sessionStorage.getItem('nowPage');
			console.log(nowPage)
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.set('token',this.getToken());
			this.param.set('currentPage',this.activePage);
			this.param.set('pageNum',10);
			this.param.set('courseId',this.courseId);
			this.getAcount();
		},
		// 获取账号列表
		getAcount(param){
			var that = this;
			var sparam = param? param: this.param;
			this.axios.post(this.$baseUrl + '/course/progress',sparam)
			.then((res)=>{
				console.log(res.data);
				var theJSON = {};
		        that.TableList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		acount: perData.student.username,
		        		name: perData.student.name,
		        		phone: perData.student.phone? perData.student.phone: '未设置',
		        		graduated: perData.graduated == 0 ? '待结业': '已结业',
		        		percent: perData.learningProgress
		        	}
		        	return theJSON;
		        }
				if (res.data.code == 200) {
					res = res.data.data;
					that.totalNum = res.totalNumber;
					if (Array.isArray(res.pageData)) {
		        		for(var key of res.pageData){
			        		that.TableList.push(formatData(key));
			        	}
			        	that.noData = false;
		        	} else {
		        		that.$message.warning('学生进度列表为空！');
		        	}
		        	if (res.totalNumber > 10) {
		        		that.ispage = true;
		        	}
		        } else {
		        	that.$message.error('获取'+ that.name +'列表失败！');
		        }
			})
			.catch((e)=>{
				console.log(e)
			})
		},//查找
		Searching(){
			var that = this;
			var param = new FormData();
			if (this.searches == '') {
				this.$message.error('请输入查找信息！');
				return ;
			} 
			if (this.searches[0] <= '9' && this.searches[0] >= '0') {
				param.set('stuNum',this.searches);
			} else {
				param.set('name',this.searches);
			}
			param.set('token',this.param.get('token'));
			param.set('courseId',this.courseId);
			if (this.isearch == false) {
				this.isearch = true;
				this.activePage = 1;
				param.set('currentPage',1);
			} else {
				param.set('currentPage',this.activePage);
			}
			param.set('pageNum',this.param.get('pageNum'));
			console.log(param.get('pageNum'))
			this.getAcount(param);
		},
		//查看所有学生
		allStu(){
    		if (this.isSearchnow == true) {
				this.isSearchnow = false;
				this.activePage = 1;
				this.searches = '';
				this.param.set('currentPage',1);
			} else {
				this.param.set('currentPage',this.activePage);
			}
    		this.getAcount();
    	},
		//获取token验证,token 不能有" "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
p{
	margin: 0;
	padding: 0;
}
.StuProgress{
	width: 95%;
	margin: 0 auto;
	.stuHead{
		height: 70*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		li{
			text-align: center;
			height: 45*@allwidth;
			line-height: 45*@allwidth;
			padding: 0 12*@allwidth;
			color: #fff;
			background: #099c4a;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 20*@allwidth;
		}
		li:hover{
			opacity: 0.8;
		}
		.search{
			border: 1px solid grey;
			border-radius: 5px;
			input{
				width: 250*@allwidth;
				height: 40*@allwidth;
				line-height: 40*@allwidth;
				padding: 0 10*@allwidth;
				font-size: 18*@allwidth;
				border-radius: 5px;
				outline: none;
				border: 0;
			}
			i{
				font-size: 22*@allwidth;
				font-weight: bold;
				padding-right: 10*@allwidth;
				cursor: pointer;
			}
			i:hover{
				color: red;
			}
		}
		.search:hover{
			border-color: #409eff;
		}
	}
	.stuTable{
		margin: 10*@allwidth 0;
		li .el-row {
			border-bottom: 1px solid #D2D2D2;
			text-align: center;
			line-height: 33*@allwidth;
			.el-col{
				justify-content: center;
				font-size: 18*@allwidth;
			}
		}
		li:first-child{
			.el-row{
				background: rgba(144, 147, 153,0.3);
			}
		}
		li:hover{
			background: rgba(144, 147, 153,0.1);
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}
.notice{
	width: 99%;
	margin: 10*@allwidth auto;
	height: calc(100vh - 190*@allwidth);
	min-height: 200px;
	box-shadow: 0 0 0.3px 0.3px #5D8486;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	img{
		width: 70px;
		height: 70px;
	}
	p{
		font-size: 18*@allwidth;
		margin: 5*@allwidth;
		color: #5D8486;
	}
}
</style>